  --select-focus-border: rgb(90, 150, 200);
  --select-focus-shadow: 0px 0px 2px 2px rgba(110, 160, 210, 0.9);
  --select-arrow-color: rgb(57, 57, 57);
  /* Search Field */
  --search-field-gradient: linear-gradient(
    to bottom,
    #ffffff 0%,
    #f8f8f8 100%
  );
  --search-field-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.15),
