    0 0 0 3px rgba(52, 106, 227, 0.35);
  --search-field-placeholder: #999999;
  --search-cancel-bg: #666666;
  /* Slider */
  --slider-track-gradient: linear-gradient(
    to bottom,
    rgb(147, 147, 147),
