  /* Checkbox */
  --checkbox-border: rgb(129, 129, 129);
  --checkbox-bg: rgb(250, 250, 250);
  --checkbox-gradient: linear-gradient(
    rgb(255, 255, 255) 0%,
    rgb(252, 252, 252) 25%,
    rgb(244, 244, 244) 50%,
    rgb(238, 238, 238) 51%,
    rgb(243, 243, 243) 100%
  );
  --checkbox-shadow: 0 0.5px 1px rgba(0, 0, 0, 0.1);
  --checkbox-mark-color: var(--text-color);
  --checkbox-mark-shadow: drop-shadow(-1px 1px 0 rgba(255, 255, 255, 0.8));
  --checkbox-pressed-gradient: linear-gradient(
    rgb(224, 224, 224) 0%,
    rgb(214, 214, 214) 50%,
    rgb(207, 207, 207) 51%,
    rgb(208, 208, 208) 100%
  );
  --checkbox-selected-border: rgb(100, 103, 124);
  --checkbox-selected-gradient: linear-gradient(
    rgb(228, 250, 255) 0%,
    #91bdee 50%,
    #78b0ee 51%,
    rgb(228, 250, 255) 100%
  );
  --checkbox-focus-border: rgb(104, 155, 201);
  --checkbox-focus-ring: 0 0 0 3px rgba(120, 171, 217, 1);
  --checkbox-indeterminate-shadow: drop-shadow(0 1.5px 0 rgba(255, 255, 255, 0.8));
  /* Radio */
  --radio-border: rgb(129, 129, 129);
  --radio-gradient: linear-gradient(
    rgb(255, 255, 255) 0%,
    rgb(253, 253, 253) 25%,
    rgb(244, 244, 244) 50%,
    rgb(236, 236, 236) 51%,
    rgb(243, 243, 243) 100%
  );
  --radio-pressed-gradient: linear-gradient(
    rgb(224, 224, 224) 0%,
    rgb(214, 214, 214) 50%,
    rgb(207, 207, 207) 51%,
    rgb(208, 208, 208) 100%
  );
  --radio-selected-border: var(--checkbox-selected-border);
  --radio-selected-gradient: var(--checkbox-selected-gradient);
  --radio-dot-color: var(--text-color);
  --radio-dot-shadow: 0 1px 0 rgba(255, 255, 255, 0.8);
  --radio-focus-ring: 0 0 0 3px rgba(120, 171, 217, 1);
  /* Pagination */
  --pagination-border: var(--checkbox-border);
  --pagination-bg: var(--checkbox-bg);
  --pagination-gradient: var(--radio-gradient);
  --pagination-hover-gradient: var(--checkbox-pressed-gradient);
  --pagination-active-border: var(--checkbox-selected-border);
  --pagination-active-gradient: var(--checkbox-selected-gradient);
  --pagination-focus-ring: var(--checkbox-focus-ring);
