    rgb(165, 193, 238) 0%,
    rgb(108, 161, 231) 50%,
    rgb(74, 138, 217) 50%,
    rgb(105, 167, 236) 75%,
    rgb(152, 201, 238) 100%
  );
  --pagination-arrow-color: rgb(129, 129, 129);
  /* Buttons */
  --aqua-button-primary-gradient: var(--aqua-blue-gradient);
  --aqua-button-primary-gradient-pressed: var(--aqua-blue-gradient-pressed);
  --aqua-button-primary-shadow: 0 0.375em 0.5em rgba(0, 0, 0, 0.3),
