    inset 0 0.25em 0.25em rgba(0, 0, 0, 0.4),
    inset 0 0.375em 0.5em 0.25em #BBBBBB,
    0 0 0.5em rgba(0, 0, 0, 0.25);
  --aqua-button-secondary-text: var(--text-color);
  --aqua-button-secondary-text-shadow: 0 0.25em 0.2em rgba(0, 0, 0, 0.25);
  --aqua-button-orange-gradient: linear-gradient(
    rgba(250, 172, 4, 0.85),
    rgba(254, 197, 64, 0.85),
    rgba(255, 219, 128, 0.85)
  );
  --aqua-button-orange-shadow: 0 0.375em 0.5em rgba(0, 0, 0, 0.3),
    0 0.125em 0.125em rgba(250, 172, 4, 0.5),
    inset 0 0.25em 0.5em rgba(120, 65, 0, 0.8),
    inset 0 0.375em 0.5em 0.25em rgba(250, 172, 4, 0.75);
  --aqua-button-orange-shadow-active: 0 0.375em 0.5em rgba(0, 0, 0, 0.3),
    0 0.125em 0.125em rgba(250, 172, 4, 0.5),
    inset 0 0.25em 0.5em rgba(120, 65, 0, 0.8),
