  --tree-item-selected-focused-strip-gradient: var(--list-item-selected-focused-strip-gradient);
  --tree-item-selected-focused-fill-gradient: var(--list-item-selected-focused-fill-gradient);
  --tree-item-selected-focused-text-shadow: var(--list-item-selected-focused-text-shadow);
  --tree-arrow-color: rgb(134, 134, 134);
  --tree-item-selected-solid-bg: rgb(60, 119, 212);
  --tree-item-selected-solid-text: var(--text-highlight);
  /* Listbox */
  --listbox-bg: var(--text-highlight);
  --listbox-border: #888888;
  --listbox-focus-shadow: 0 0 3px rgba(43, 153, 255, 0.5);
  --listbox-option-selected-bg: var(--aqua-blue);
  --listbox-option-selected-text: var(--text-highlight);
  --listbox-option-hover-bg: var(--surface);
  /* Fieldset */
  --fieldset-border: #888888;
  --fieldset-bg: var(--window-bg);
  /* Tooltip */
  --tooltip-bg: rgba(251, 250, 205, 0.95);
  --tooltip-border: rgba(125, 125, 125, 0.4);
  --tooltip-text: var(--text-color);
  --tooltip-shadow: 0 3px 8px rgba(0, 0, 0, 0.4);
