/**
 * Base Styles
 * Global resets and typography
 */
html {
  overscroll-behavior: none;
}

body {
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  color: var(--text-color);
  background: var(--pinstripe);
  background-color: var(--text-highlight);
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: none;
}

* {
  box-sizing: border-box;
}

a {
  color: var(--aqua-blue-dark);
}
a:hover {
  color: var(--aqua-blue);
}

/**
 * Backgrounds & Pinstripes
 * Mac OS X Aqua-style background patterns and utility classes
 */
/* CSS Variables for Aqua theming */
:root {
  /* Pinstripe patterns */
  --aqua-pinstripe-light: repeating-linear-gradient(
    0deg,
    transparent 0px,
    transparent 1.5px,
    rgba(255, 255, 255, 0.85) 1.5px,
    rgba(255, 255, 255, 0.85) 4px
  );
  --aqua-pinstripe-window: repeating-linear-gradient(
      0deg,
      transparent 0px,
      transparent 1.5px,
      rgba(255, 255, 255, 0.85) 1.5px,
      rgba(255, 255, 255, 0.85) 4px
    ),
    linear-gradient(to bottom, #ececec 0%, #ececec 100%);
  --aqua-pinstripe-titlebar: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.3) 0%,
    rgba(219, 219, 219, 0.6) 70%,
    rgba(206, 206, 206, 0.7) 100%
  );
  --aqua-pinstripe-menubar: repeating-linear-gradient(
    0deg,
    transparent 0px,
    transparent 1.5px,
    rgba(255, 255, 255, 0.55) 1.5px,
    rgba(255, 255, 255, 0.55) 4px
  );
  --aqua-pinstripe-diagonal: repeating-linear-gradient(
    -45deg,
    #eee 0px,
    #eee 2px,
    #e3e3e3 2px,
    #e3e3e3 4px
  );
  --aqua-background-classic: repeating-linear-gradient(
    #F0F0F0,
    #F0F0F0 4px,
    #FFFFFF 4px,
    #FFFFFF 8px
  );
  --aqua-background-diagonal: repeating-linear-gradient(
    45deg,
    #eee,
    #eee 10px,
    #e3e3e3 10px,
    #e3e3e3 20px
  );
  --aqua-background-gray: #e8e8e8;
  --aqua-menubar-gradient: var(--menu-bar-gradient);
  --aqua-gloss-gradient: var(--menu-bar-gloss);
  --aqua-inset-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.15),
    inset 0 0 0 1px rgba(0, 0, 0, 0.2),
    0 1px 0 rgba(255, 255, 255, 0.8);
  --aqua-text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  --aqua-text-shadow-dark: 0 -1px 0 rgba(0, 0, 0, 0.3);
  /* Brushed metal texture */
  --aqua-brushed-metal: linear-gradient(
    to bottom,
    #e8e8e8 0%,
    #d0d0d0 50%,
    #c8c8c8 100%
  );
  /* Selection colors */
  --aqua-selection-bg: rgba(39, 101, 202, 0.88);
  --aqua-selection-text: #ffffff;
  --aqua-selection-glow: rgba(48, 103, 218, 0.5);
  /* Primary blue gradient (for buttons, progress bars) */
  --aqua-blue-gradient: linear-gradient(
    rgba(0, 65, 184, 0.625),
    rgba(45, 115, 199, 0.625),
    rgba(33, 160, 196, 0.625)
  );
  /* Darker blue gradient for pressed state (10.4 and earlier) */
  --aqua-blue-gradient-pressed: linear-gradient(
    rgba(0, 45, 140, 0.75),
    rgba(25, 85, 165, 0.75),
    rgba(15, 120, 160, 0.75)
  );
  /* Secondary gray gradient */
  --aqua-gray-gradient: linear-gradient(
    rgba(160, 160, 160, 0.625),
    rgba(255, 255, 255, 0.625)
  );
  /* Window backgrounds */
  --aqua-window-bg: #ececec;
  --aqua-titlebar-bg: linear-gradient(to bottom, #e8e8e8 0%, #d0d0d0 100%);
  --aqua-titlebar-inactive-bg: linear-gradient(to bottom, #f6f6f6 0%, #e0e0e0 100%);
  /* Border colors */
  --aqua-border-color: rgba(0, 0, 0, 0.3);
  --aqua-border-color-light: rgba(0, 0, 0, 0.15);
  /* Focus ring */
  --focus-outer: rgba(52, 106, 227, 0.45);
  --focus-inner: rgba(180, 210, 255, 0.8);
  --aqua-focus-ring: 0 0 0 3px var(--focus-outer), 0 0 0 1px var(--focus-inner);
  /* Shadows */
  --aqua-window-shadow: 0 8px 25px rgba(0, 0, 0, 0.4), 0 2px 8px rgba(0, 0, 0, 0.2);
  --aqua-window-shadow-inactive: 0 4px 15px rgba(0, 0, 0, 0.2), 0 1px 4px rgba(0, 0, 0, 0.1);
  --aqua-button-shadow: 0 2px 3px rgba(0, 0, 0, 0.3), 0 1px 1px rgba(0, 78, 187, 0.5);
}

/* Classic horizontal stripes (like Finder windows) */
.aqua-background {
  background: var(--aqua-background-classic);
}

/* Diagonal stripes */
.aqua-background--diagonal {
  background: var(--aqua-background-diagonal);
}

/* Solid gray */
.aqua-background--gray {
  background: var(--aqua-background-gray);
}

/* Brushed metal */
.aqua-background--metal {
  background: var(--aqua-brushed-metal);
}

/* Full viewport wrapper */
.aqua-background--full {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  box-sizing: border-box;
}

/* Utility classes for backgrounds */
.aqua-bg-window {
  background-image: var(--aqua-pinstripe-window);
}

.aqua-bg-pinstripe {
  background-image: var(--aqua-pinstripe-light);
  background-color: var(--aqua-window-bg);
}

.aqua-bg-pinstripe-diagonal {
  background-image: var(--aqua-pinstripe-diagonal);
}

.aqua-bg-titlebar {
  background-image: var(--aqua-pinstripe-titlebar), var(--aqua-titlebar-bg);
}

.aqua-bg-menubar {
  background-image: var(--aqua-pinstripe-menubar), var(--aqua-menubar-gradient);
}

.aqua-bg-brushed-metal {
  background: var(--aqua-brushed-metal);
}

/* ============================================
   Authentic Brushed Metal Backgrounds
   Mac OS X 10.3-10.5 era styling (2003-2007)
   ============================================ */
/* Full authentic brushed metal (iTunes/QuickTime style) */
.aqua-bg-brushed-metal-authentic {
  background: var(--brushed-metal-authentic);
  background-color: #A0A0A0;
}

/* Light brushed metal variant */
.aqua-bg-brushed-metal-light {
  background: var(--brushed-metal-light-variant);
  background-color: #BCBCBC;
}

/* Dark brushed metal variant */
.aqua-bg-brushed-metal-dark {
  background: var(--brushed-metal-dark-variant);
  background-color: #888888;
}

/* Warm brushed metal variant (early QuickTime) */
.aqua-bg-brushed-metal-warm {
  background: var(--brushed-metal-warm);
  background-color: #A8A098;
}

/* Brushed metal titlebar background */
.aqua-bg-brushed-metal-titlebar {
  background: var(--brushed-metal-titlebar);
  background-color: #B4B4B4;
}

/* Brushed metal toolbar background */
.aqua-bg-brushed-metal-toolbar {
  background: var(--brushed-metal-toolbar);
  background-color: #B0B0B0;
}

.aqua-bg-solid {
  background: var(--aqua-window-bg);
}

/* Utility classes for borders */
.aqua-border {
  border: 1px solid var(--aqua-border-color);
}

.aqua-border-light {
  border: 1px solid var(--aqua-border-color-light);
}

.aqua-border-bottom {
  border-bottom: 1px solid var(--aqua-border-color);
}

/* Utility classes for shadows */
.aqua-shadow-window {
  box-shadow: var(--aqua-window-shadow);
}

.aqua-shadow-window-inactive {
  box-shadow: var(--aqua-window-shadow-inactive);
}

.aqua-shadow-button {
  box-shadow: var(--aqua-button-shadow);
}

/* Focus ring utility */
.aqua-focus-ring:focus {
  outline: none;
  box-shadow: var(--aqua-focus-ring);
}

.aqua-focus-ring.is-focused {
  outline: none;
  box-shadow: var(--aqua-focus-ring);
}

/* Selection color utility */
.aqua-selection::selection {
  background: var(--aqua-selection-bg);
  color: var(--aqua-selection-text);
}

/* Gloss effect utility (for buttons, bars) */
.aqua-gloss {
  position: relative;
}

.aqua-gloss::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 50%;
  background: var(--aqua-gloss-gradient);
  pointer-events: none;
  z-index: 1;
}

/* Inset utility for inputs */
.aqua-inset {
  box-shadow: var(--aqua-inset-shadow);
}

/* Text shadows */
.aqua-text-shadow {
  text-shadow: var(--aqua-text-shadow);
}

.aqua-text-shadow-dark {
  text-shadow: var(--aqua-text-shadow-dark);
}

/* Font family utility */
.aqua-font {
  font-family: "Lucida Grande", "Lucida Sans Unicode", sans-serif;
  -webkit-font-smoothing: antialiased;
}

/* Rounded corners utilities */
.aqua-rounded {
  border-radius: 8px;
}

.aqua-rounded-top {
  border-radius: 8px 8px 0 0;
}

.aqua-rounded-pill {
  border-radius: 9999px;
}

/* Disabled state utility */
.aqua-disabled {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}

/* Core Components */
/**
 * Window
 * Mac OS X Aqua-style window with titlebar and traffic light controls
 */
.window {
  display: flex;
  flex-direction: column;
  border-radius: 8px 8px 0 0;
  overflow: hidden;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  /* Window border */
  border: 1px solid var(--window-border);
  /* Window shadow */
  box-shadow: var(--aqua-window-shadow);
}
.window.inactive {
  border-color: var(--window-border-inactive);
  box-shadow: var(--aqua-window-shadow-inactive);
}
.window.inactive .title-bar {
  background: var(--aqua-titlebar-inactive-bg);
  border-bottom-color: var(--window-titlebar-border-inactive);
}
.window.inactive .title-bar-controls button {
  background: var(--traffic-light-inactive-gradient) !important;
  box-shadow: var(--traffic-light-inactive-shadow) !important;
}
.window.inactive .title-bar-text {
  color: var(--window-titlebar-text-inactive);
}
.window.brushed-metal .window-body {
  background: var(--window-toolbar-bg);
  background-image: none;
}
.window {
  /* ============================================
     Authentic Brushed Metal Window
     Mac OS X 10.3-10.5 era (iTunes, QuickTime, Finder)
     ============================================ */
}
.window.brushed-metal-authentic {
  border-color: var(--brushed-metal-border);
  box-shadow: var(--brushed-metal-window-shadow);
}
.window.brushed-metal-authentic .title-bar {
  background: var(--brushed-metal-titlebar);
  background-color: #B4B4B4;
  border-bottom-color: var(--brushed-metal-border-light);
  box-shadow: var(--brushed-metal-shadow);
}
.window.brushed-metal-authentic .title-bar-text {
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.4);
}
.window.brushed-metal-authentic .window-body {
  background: var(--brushed-metal-authentic);
  background-color: #A0A0A0;
}
.window.brushed-metal-authentic .toolbar {
  background: var(--brushed-metal-toolbar);
  background-color: #B0B0B0;
  border-bottom-color: var(--brushed-metal-border-light);
  box-shadow: var(--brushed-metal-shadow);
}
.window.brushed-metal-authentic .status-bar {
  background: var(--brushed-metal-toolbar);
  background-color: #B0B0B0;
  border-top-color: var(--brushed-metal-border-light);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.3);
}
.window.brushed-metal-authentic.inactive .title-bar {
  background: var(--brushed-metal-light-variant);
  background-color: #BCBCBC;
}
.window {
  /* Light brushed metal variant */
}
.window.brushed-metal-light {
  border-color: var(--brushed-metal-border-light);
}
.window.brushed-metal-light .title-bar {
  background: var(--brushed-metal-light-variant);
  background-color: #BCBCBC;
  border-bottom-color: #A0A0A0;
}
.window.brushed-metal-light .window-body {
  background: var(--brushed-metal-light-variant);
  background-color: #BCBCBC;
}
.window.brushed-metal-light .toolbar {
  background: var(--brushed-metal-light-variant);
  background-color: #BCBCBC;
}
.window {
  /* Dark brushed metal variant (like Finder sidebar) */
}
.window.brushed-metal-dark {
  border-color: #505050;
}
.window.brushed-metal-dark .title-bar {
  background: var(--brushed-metal-dark-variant);
  background-color: #888888;
  border-bottom-color: #606060;
}
.window.brushed-metal-dark .title-bar-text {
  color: #FFFFFF;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.4);
}
.window.brushed-metal-dark .window-body {
  background: var(--brushed-metal-dark-variant);
  background-color: #888888;
}
.window.brushed-metal-dark .toolbar {
  background: var(--brushed-metal-dark-variant);
  background-color: #888888;
}
.window {
  /* Warm brushed metal variant (early QuickTime Player) */
}
.window.brushed-metal-warm {
  border-color: #787068;
}
.window.brushed-metal-warm .title-bar {
  background: var(--brushed-metal-warm);
  background-color: #A8A098;
  border-bottom-color: #908880;
}
.window.brushed-metal-warm .window-body {
  background: var(--brushed-metal-warm);
  background-color: #A8A098;
}

.window-body {
  flex: 1;
  padding: 16px;
  overflow: auto;
  background: var(--window-bg);
}

/* Title Bar */
.title-bar {
  display: flex;
  align-items: center;
  height: 22px;
  padding: 0 8px;
  gap: 8px;
  /* Gradient background with pinstripes */
  background: var(--aqua-titlebar-bg);
  background-image: var(--aqua-pinstripe-titlebar), var(--aqua-titlebar-bg);
  border-bottom: 1px solid var(--window-titlebar-border);
}

.title-bar-text {
  flex: 1;
  text-align: center;
  font-size: var(--font-size-title);
  font-weight: 500;
  color: var(--window-titlebar-text);
  text-shadow: var(--window-titlebar-text-shadow);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  user-select: none;
}

/* Spacer to center title */
.title-bar-spacer {
  width: 54px;
}

/* Traffic Light Controls */
.title-bar-controls {
  display: flex;
  gap: 8px;
}
.title-bar-controls:hover button {
  /* Show icons on hover */
}
.title-bar-controls:hover button .icon {
  opacity: 1;
}
.title-bar-controls button {
  position: relative;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: none;
  padding: 0;
  min-width: 0;
  min-height: 0;
  cursor: pointer;
  overflow: visible;
  transition: all 0.15s ease;
  background: none;
  font-size: 14px;
  /* Icon inside button */
}
.title-bar-controls button .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
  font-size: 8px;
  font-weight: bold;
  line-height: 1;
  opacity: 0;
  z-index: 10;
  transition: opacity 0.1s ease;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}
.title-bar-controls button {
  /* Aqua glass effect (top shine + bottom glow) */
}
.title-bar-controls button::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 40%;
  width: 75%;
  top: 8%;
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
  border-radius: 50% 50% 30% 30%;
  filter: blur(0.5px);
  z-index: 2;
  pointer-events: none;
}
.title-bar-controls button::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 35%;
  width: 70%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.35));
  border-radius: 30% 30% 50% 50%;
  filter: blur(1px);
  pointer-events: none;
}
.title-bar-controls button {
  /* Close button - Red */
}
.title-bar-controls button[aria-label=Close] {
  background: var(--traffic-light-red-gradient);
  box-shadow: var(--traffic-light-red-shadow);
}
.title-bar-controls button[aria-label=Close] .icon {
  color: var(--traffic-light-icon-red);
  text-shadow: var(--traffic-light-icon-red-shadow);
}
.title-bar-controls button[aria-label=Close]:hover {
  background: var(--traffic-light-red-hover-gradient);
}
.title-bar-controls button {
  /* Minimize button - Yellow */
}
.title-bar-controls button[aria-label=Minimize] {
  background: var(--traffic-light-yellow-gradient);
  box-shadow: var(--traffic-light-yellow-shadow);
}
.title-bar-controls button[aria-label=Minimize] .icon {
  color: var(--traffic-light-icon-yellow);
  text-shadow: var(--traffic-light-icon-yellow-shadow);
}
.title-bar-controls button[aria-label=Minimize]:hover {
  background: var(--traffic-light-yellow-hover-gradient);
}
.title-bar-controls button {
  /* Zoom/Maximize button - Green */
}
.title-bar-controls button[aria-label=Maximize], .title-bar-controls button[aria-label=Zoom] {
  background: var(--traffic-light-green-gradient);
  box-shadow: var(--traffic-light-green-shadow);
}
.title-bar-controls button[aria-label=Maximize] .icon, .title-bar-controls button[aria-label=Zoom] .icon {
  color: var(--traffic-light-icon-green);
  text-shadow: var(--traffic-light-icon-green-shadow);
}
.title-bar-controls button[aria-label=Maximize]:hover, .title-bar-controls button[aria-label=Zoom]:hover {
  background: var(--traffic-light-green-hover-gradient);
}
.title-bar-controls button:active:not(:disabled) {
  filter: brightness(0.85);
}

/* Toolbar */
.toolbar {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 8px;
  min-height: 32px;
  /* Brushed metal texture */
  background: var(--window-toolbar-bg);
  border-bottom: 1px solid var(--window-toolbar-border);
  box-shadow: var(--window-toolbar-shadow);
}

/* Status Bar */
.status-bar {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 8px;
  min-height: 20px;
  font-size: var(--font-size-small);
  color: var(--window-status-text);
  background: var(--window-status-bg);
  border-top: 1px solid var(--window-status-border);
  box-shadow: var(--window-status-shadow);
}

/* Sheet variant (modal attached to window) */
.window.sheet {
  border-radius: 0 0 8px 8px;
  box-shadow: var(--window-sheet-shadow);
}
.window.sheet .title-bar {
  display: none;
}

/* Utility window variant (smaller, no pinstripes) */
.window.utility {
  border-radius: 4px 4px 0 0;
}
.window.utility .title-bar {
  height: 16px;
  padding: 0 4px;
  gap: 4px;
}
.window.utility .title-bar-controls button {
  width: 10px;
  height: 10px;
}
.window.utility .title-bar-text {
  font-size: 11px;
}
.window.utility .title-bar-spacer {
  width: 38px;
}
.window.utility .window-body {
  background: var(--window-bg);
  background-image: none;
}

/**
 * Traffic Lights
 * Mac OS X Aqua-style window control buttons (close, minimize, zoom)
 */
/* Traffic Lights Container */
.traffic-lights {
  display: flex;
  gap: 8px;
  align-items: center;
}

/* Base Aqua Light Style */
.aqua-light {
  --button-size: 14px;
  position: relative;
  width: var(--button-size);
  height: var(--button-size);
  font-size: var(--button-size);
  border-radius: 50%;
  border: none;
  padding: 0;
  cursor: pointer;
  transition: all 0.15s ease;
  overflow: hidden;
  /* Gray/inactive state by default */
  background: var(--traffic-light-inactive-gradient);
  box-shadow: var(--traffic-light-inactive-shadow);
  /* Aqua glass effect (top shine + bottom glow) */
}
.aqua-light::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 40%;
  width: 75%;
  top: 8%;
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
  border-radius: 50% 50% 30% 30%;
  filter: blur(0.5px);
  z-index: 2;
  pointer-events: none;
}
.aqua-light::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 35%;
  width: 70%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.35));
  border-radius: 30% 30% 50% 50%;
  filter: blur(1px);
  pointer-events: none;
}

/* Icon styles - hidden by default */
.aqua-light .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
  font-size: 1em;
  font-weight: bold;
  line-height: 1;
  opacity: 0;
  z-index: 10;
  transition: opacity 0.1s ease;
}

.aqua-red .icon {
  color: var(--traffic-light-icon-red);
  text-shadow: var(--traffic-light-icon-red-shadow);
}

.aqua-yellow .icon {
  color: var(--traffic-light-icon-yellow);
  text-shadow: var(--traffic-light-icon-yellow-shadow);
}

.aqua-green .icon {
  color: var(--traffic-light-icon-green);
  text-shadow: var(--traffic-light-icon-green-shadow);
}

/* Show ALL icons when hovering over the container */
.traffic-lights:hover .aqua-light:not(.aqua-inactive) .icon {
  opacity: 1;
}

/* Close - Red (on hover) */
.traffic-lights:hover .aqua-red:not(.aqua-inactive) {
  background: var(--traffic-light-red-gradient);
  box-shadow: var(--traffic-light-red-shadow);
}

/* Minimize - Yellow/Orange (on hover) */
.traffic-lights:hover .aqua-yellow:not(.aqua-inactive) {
  background: var(--traffic-light-yellow-gradient);
  box-shadow: var(--traffic-light-yellow-shadow);
}

/* Maximize - Green (on hover) */
.traffic-lights:hover .aqua-green:not(.aqua-inactive) {
  background: var(--traffic-light-green-gradient);
  box-shadow: var(--traffic-light-green-shadow);
}

/* Permanently inactive */
.aqua-inactive {
