/**
 * Forms
 * Mac OS X Aqua-style text inputs, textareas, selects, labels
 */
/* Text Inputs */
input[type=text],
input[type=password],
input[type=email],
input[type=url],
input[type=tel],
input[type=number],
.aqua-textfield,
textarea {
  height: 20px;
  font-size: 10pt;
  font-family: "Lucida Grande", "Lucida Sans Unicode", var(--font-system);
  padding: 0px 4px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 1px solid var(--input-border);
  border-top-color: var(--input-border-top);
  box-shadow: var(--input-shadow);
  transition: none;
  border-radius: 0;
  outline: none;
  background: var(--input-bg);
  color: var(--text-color);
}
input[type=text]::selection,
input[type=password]::selection,
input[type=email]::selection,
input[type=url]::selection,
input[type=tel]::selection,
input[type=number]::selection,
.aqua-textfield::selection,
textarea::selection {
  background: var(--input-selection-bg) !important;
}
input[type=text]::-moz-selection,
input[type=password]::-moz-selection,
input[type=email]::-moz-selection,
input[type=url]::-moz-selection,
input[type=tel]::-moz-selection,
input[type=number]::-moz-selection,
.aqua-textfield::-moz-selection,
textarea::-moz-selection {
  background: var(--input-selection-bg) !important;
}
input[type=text]:focus,
input[type=password]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=tel]:focus,
input[type=number]:focus,
.aqua-textfield:focus,
textarea:focus {
  border-radius: 2px;
  border-color: var(--input-focus-border);
  box-shadow: var(--input-focus-shadow);
}
input[type=text]::placeholder,
input[type=password]::placeholder,
input[type=email]::placeholder,
input[type=url]::placeholder,
input[type=tel]::placeholder,
input[type=number]::placeholder,
.aqua-textfield::placeholder,
textarea::placeholder {
  color: var(--input-placeholder);
}
input[type=text]:disabled,
input[type=password]:disabled,
input[type=email]:disabled,
input[type=url]:disabled,
input[type=tel]:disabled,
input[type=number]:disabled,
.aqua-textfield:disabled,
textarea:disabled {
  background: var(--input-disabled-bg);
  color: var(--input-disabled-text);
  cursor: not-allowed;
}

.aqua-textfield.round-rect {
  border-radius: 11px;
  padding: 0 10px;
}

.aqua-textfield.search {
  border-radius: 11px;
  padding-left: 25px;
  padding-right: 10px;
  background-image: var(--search-icon);
  background-repeat: no-repeat;
  background-position: 7px center;
}

textarea {
  resize: vertical;
  min-height: 60px;
  height: auto;
  padding: 4px;
}

/* Size variants */
input.small {
  height: 16px;
  font-size: 9pt;
  padding: 0px 3px;
}

input.large {
  height: 26px;
  font-size: 12pt;
  padding: 0px 6px;
}

/* Select Dropdown */
select {
  position: relative;
  height: 20px;
  min-width: 130px;
  padding: 0 24px 0 10px;
  font-size: 10pt;
  font-family: "Lucida Grande", "Lucida Sans Unicode", var(--font-system);
  border: 1px solid var(--select-border);
  border-radius: 4px;
  background: transparent;
  background-image: var(--select-gradient);
  box-shadow: var(--select-shadow);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  outline: none;
  color: var(--text-color);
  /* Aqua glass effect */
}
select::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  top: 5%;
  background: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.3));
  border-radius: inherit;
  filter: blur(1px);
  z-index: 1;
  pointer-events: none;
}
select::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5));
  border-radius: inherit;
  filter: blur(3px);
  z-index: 1;
  pointer-events: none;
}
select:focus {
  border-color: var(--select-focus-border);
  box-shadow: var(--select-focus-shadow);
}
select:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Select wrapper for custom arrows */
.select-wrapper {
  position: relative;
  display: inline-block;
}
.select-wrapper::before, .select-wrapper::after {
  content: "";
  position: absolute;
  z-index: 2;
  top: 5px;
  right: 7px;
  width: 0;
  height: 0;
  border: 3px dashed;
  border-color: var(--select-arrow-color) transparent;
  pointer-events: none;
}
.select-wrapper::before {
  border-bottom-style: solid;
  border-top: none;
}
.select-wrapper::after {
  margin-top: 7px;
  border-top-style: solid;
  border-bottom: none;
}
.select-wrapper select {
  width: 100%;
}

/* Labels */
label {
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  color: var(--text-color);
  user-select: none;
  cursor: default;
}
label.disabled {
  color: var(--text-disabled);
}

/* Search Field - Rounded Aqua style */
input[type=search],
.search-field {
  position: relative;
  height: 22px;
  min-width: 180px;
  padding: 0 24px;
  border-radius: 11px;
  background: var(--search-field-gradient);
  box-shadow: var(--search-field-shadow);
  font-family: var(--font-system);
  font-size: 12px;
  border: none;
  -webkit-appearance: none;
  appearance: none;
  transition: box-shadow 0.15s ease;
}
input[type=search]:focus,
.search-field:focus {
  outline: none;
  box-shadow: var(--search-field-focus-shadow);
}
input[type=search]::placeholder,
.search-field::placeholder {
  color: var(--search-field-placeholder);
}
input[type=search]:disabled,
.search-field:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
input[type=search]::-webkit-search-cancel-button,
.search-field::-webkit-search-cancel-button {
  -webkit-appearance: none;
  width: 14px;
  height: 14px;
  background: var(--search-cancel-bg);
  border-radius: 50%;
  cursor: pointer;
  opacity: 0.7;
  margin-right: -4px;
}
input[type=search]::-webkit-search-cancel-button:hover,
.search-field::-webkit-search-cancel-button:hover {
  opacity: 1;
}

/* ============================================
   SLIDER / RANGE INPUT - JavaFX AquaFX accurate
   ============================================ */
input[type=range],
.aqua-slider,
.slider {
  -webkit-appearance: none;
  appearance: none;
  width: 200px;
  height: 4px;
  background: var(--slider-track-gradient);
  border: 0.5px solid;
  border-color: var(--slider-track-border);
  border-radius: 2.5px;
  outline: none;
  /* Webkit thumb (Chrome, Safari, Edge) */
}
input[type=range]::-webkit-slider-thumb,
.aqua-slider::-webkit-slider-thumb,
.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 17px;
  height: 17px;
  border: 0.5px solid var(--slider-thumb-border);
  border-radius: 50%;
  background: var(--slider-thumb-gradient);
  cursor: pointer;
  box-shadow: var(--slider-thumb-shadow);
}
input[type=range]::-webkit-slider-thumb:active,
.aqua-slider::-webkit-slider-thumb:active,
.slider::-webkit-slider-thumb:active {
  background: var(--slider-thumb-active-gradient);
}
input[type=range],
.aqua-slider,
.slider {
  /* Mozilla thumb (Firefox) */
}
input[type=range]::-moz-range-thumb,
.aqua-slider::-moz-range-thumb,
.slider::-moz-range-thumb {
  width: 17px;
  height: 17px;
  border: 0.5px solid var(--slider-thumb-border);
  border-radius: 50%;
  background: var(--slider-thumb-gradient);
  cursor: pointer;
  box-shadow: var(--slider-thumb-shadow);
}
input[type=range]::-moz-range-thumb:active,
.aqua-slider::-moz-range-thumb:active,
.slider::-moz-range-thumb:active {
  background: var(--slider-thumb-active-gradient);
}
input[type=range],
.aqua-slider,
.slider {
  /* Mozilla track */
}
input[type=range]::-moz-range-track,
.aqua-slider::-moz-range-track,
.slider::-moz-range-track {
  height: 4px;
  background: var(--slider-track-gradient);
  border: 0.5px solid;
  border-color: var(--slider-track-border);
  border-radius: 2.5px;
}
input[type=range],
.aqua-slider,
.slider {
  /* Focus state */
}
input[type=range]:focus::-webkit-slider-thumb,
.aqua-slider:focus::-webkit-slider-thumb,
.slider:focus::-webkit-slider-thumb {
  box-shadow: var(--slider-focus-ring);
}
input[type=range]:focus::-moz-range-thumb,
.aqua-slider:focus::-moz-range-thumb,
.slider:focus::-moz-range-thumb {
  box-shadow: var(--slider-focus-ring);
}
input[type=range]:disabled,
.aqua-slider:disabled,
.slider:disabled {
  opacity: 0.5;
  pointer-events: none;
  cursor: not-allowed;
}
input[type=range]:disabled::-webkit-slider-thumb,
.aqua-slider:disabled::-webkit-slider-thumb,
.slider:disabled::-webkit-slider-thumb {
  cursor: not-allowed;
}
input[type=range]:disabled::-moz-range-thumb,
.aqua-slider:disabled::-moz-range-thumb,
.slider:disabled::-moz-range-thumb {
  cursor: not-allowed;
}

/* Vertical slider */
input[type=range].vertical,
.aqua-slider.vertical,
.slider.vertical {
  writing-mode: vertical-lr;
  direction: rtl;
  width: 4px;
  height: 150px;
  background: var(--slider-track-gradient-vertical);
}

/* ============================================
   Custom slider (showcase)
   ============================================ */
.aqua-slider-custom {
  position: relative;
  width: 200px;
  height: 20px;
  display: flex;
  align-items: center;
}
.aqua-slider-custom .track {
  width: 100%;
  height: 4px;
  background: var(--slider-track-gradient);
