}
.aqua-slider-custom .thumb {
  position: absolute;
  width: 17px;
  height: 17px;
  border: 0.5px solid var(--slider-thumb-border);
  border-radius: 50%;
  background: var(--slider-thumb-gradient);
  cursor: pointer;
  box-shadow: var(--slider-thumb-shadow);
  top: 50%;
  transform: translateY(-50%);
  left: 50%;
  overflow: hidden;
