  pointer-events: none;
}
.aqua-slider-custom .thumb:active {
  background: var(--slider-thumb-active-gradient);
}
.aqua-slider-custom .ticks {
  position: absolute;
  width: 100%;
  top: 100%;
  margin-top: 6px;
  display: flex;
  justify-content: space-between;
  padding: 0 8px;
  box-sizing: border-box;
}
.aqua-slider-custom .tick {
  width: 1px;
  height: 4px;
  background: var(--slider-tick);
}
.aqua-slider-custom.disabled {
  opacity: 0.5;
  pointer-events: none;
}
.aqua-slider-custom.vertical {
  width: 20px;
  height: 150px;
  flex-direction: column;
}
.aqua-slider-custom.vertical .track {
  width: 4px;
  height: 100%;
  background: var(--slider-track-gradient-vertical);
  border-color: var(--slider-track-border);
}
.aqua-slider-custom.vertical .thumb {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.aqua-slider-custom.vertical .ticks {
  top: 0;
  left: 100%;
  margin-left: 6px;
  margin-top: 0;
  width: auto;
  height: 100%;
  flex-direction: column;
