}
.aqua-slider-custom.vertical .tick {
  width: 4px;
  height: 1px;
}
.aqua-slider-custom.alternative-thumb .thumb {
  width: 17px;
  height: 19px;
  border: none;
  border-radius: 0;
  background: none;
  box-shadow: none;
  transform: translateY(-35%);
  background-image: var(--slider-thumb-alt-image);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.aqua-slider-custom.alternative-thumb .thumb:active {
  background-image: var(--slider-thumb-alt-active-image);
}
.aqua-slider-custom.alternative-thumb.vertical .thumb {
  width: 19px;
  height: 17px;
  transform: translate(-35%, -50%);
  background-image: var(--slider-thumb-alt-vertical-image);
}
.aqua-slider-custom.alternative-thumb.vertical .thumb:active {
  background-image: var(--slider-thumb-alt-vertical-active-image);
}
.aqua-slider-custom.show-ticks {
  margin-bottom: 25px;
}
.aqua-slider-custom.show-ticks.vertical {
  margin-bottom: 0;
  margin-right: 30px;
}

/* Input with label wrapper */
.input-group,
