/**
 * Checkboxes
 * Mac OS X Aqua-style checkboxes - JavaFX AquaFX accurate
 */
/* Class-based checkbox (recommended) */
.checkbox {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
}
.checkbox input {
  display: none;
}
.checkbox {
  /* The checkbox box */
}
.checkbox .box {
  width: var(--checkbox-size);
  height: var(--checkbox-size);
  border: 0.5px solid var(--checkbox-border);
  border-radius: 2.5px;
  background: var(--checkbox-bg);
  background: var(--checkbox-gradient);
  background-clip: padding-box;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  box-shadow: var(--checkbox-shadow);
  flex-shrink: 0;
  overflow: visible;
  /* Aqua glass effect */
}
.checkbox .box::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  top: 5%;
  background: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.3));
  border-radius: inherit;
  filter: blur(1px);
  z-index: 1;
  pointer-events: none;
}
.checkbox .box::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5));
  border-radius: inherit;
  filter: blur(3px);
  z-index: 1;
  pointer-events: none;
}
.checkbox {
  /* Checkmark - positioned to extend outside the box (classic Aqua style) */
}
.checkbox .box .mark {
  display: none;
  position: absolute;
  z-index: 2;
  inset: -3.5px -2.5px 2.5px 1px;
  background-image: url("data:image/svg+xml,%3Csvg%20xmlns%3D%27http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%27%20viewBox%3D%270%200%2012%2012%27%3E%3Cpath%20d%3D%27m%2018.174523%2C1027.137%20c%20-0.18077%2C-0.4575%20-0.184364%2C-0.8913%200.115901%2C-1.1721%200.300265%2C-0.2809%200.836622%2C-0.3601%201.288422%2C-0.041%200.4518%2C0.3191%202.020453%2C2.9316%202.020453%2C2.9316%20l%205.41194%2C-8.0232%20c%20-4e-6%2C0%200.516257%2C-0.6671%201.248682%2C-0.3099%200.648831%2C0.3165%200.559153%2C1.0373%200.559153%2C1.0373%200%2C0%20-5.940433%2C9.3556%20-6.15501%2C9.6287%20-0.214577%2C0.273%20-1.595078%2C0.2481%20-1.817995%2C-0.027%20-0.222917%2C-0.2751%20-2.490777%2C-3.567%20-2.671546%2C-4.0244%20z%27%20transform%3D%27translate%28-17%20-1019%29%27%20fill%3D%27black%27%2F%3E%3C%2Fsvg%3E");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-color: transparent;
  filter: var(--checkbox-mark-shadow);
}
.checkbox {
  /* Armed/pressed state */
}
.checkbox:active .box {
  background: var(--checkbox-bg);
  background: var(--checkbox-pressed-gradient);
}
.checkbox {
  /* Checked state */
}
.checkbox input:checked + .box {
  border-color: var(--checkbox-selected-border);
  background: var(--checkbox-selected-gradient);
}
.checkbox input:checked + .box .mark {
  display: block;
}
.checkbox {
  /* Focus state */
}
.checkbox input:focus + .box {
  border-color: var(--checkbox-focus-border);
  box-shadow: var(--checkbox-focus-ring);
}
.checkbox {
  /* Disabled state */
}
.checkbox.disabled, .checkbox:has(input:disabled) {
  opacity: 0.6;
  cursor: default;
  pointer-events: none;
}
.checkbox {
  /* Indeterminate state */
}
.checkbox input:indeterminate + .box {
  border-color: var(--checkbox-selected-border);
  background: var(--checkbox-selected-gradient);
}
.checkbox input:indeterminate + .box .mark {
  display: block;
  width: 8px;
  height: 2px;
  background-image: none;
  background-color: var(--checkbox-mark-color);
  border-radius: 0;
  filter: var(--checkbox-indeterminate-shadow);
