/**
 * Radio Buttons
 * Mac OS X Aqua-style radio buttons - JavaFX AquaFX accurate
 */
/* Class-based radio button (recommended) */
.radio {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
}
.radio input {
  display: none;
}
.radio {
  /* The radio circle */
}
.radio .circle {
  width: var(--radio-size);
  height: var(--radio-size);
  border: 0.5px solid var(--radio-border);
  border-radius: 50%;
  background: var(--checkbox-bg);
  background: var(--radio-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1px;
  flex-shrink: 0;
  position: relative;
  /* Aqua glass effect */
}
.radio .circle::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 40%;
  width: 75%;
  top: 8%;
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
  border-radius: 50% 50% 30% 30%;
  filter: blur(0.5px);
  z-index: 2;
  pointer-events: none;
}
.radio .circle::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 35%;
  width: 70%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.35));
  border-radius: 30% 30% 50% 50%;
  filter: blur(1px);
  pointer-events: none;
}
.radio {
  /* The inner dot */
}
.radio .circle .dot {
  width: 6px;
  height: 6px;
  background: transparent;
  border-radius: 50%;
}
.radio {
  /* Armed/pressed state */
}
.radio:active .circle {
  background: var(--radio-pressed-gradient);
}
.radio {
  /* Selected state */
}
.radio input:checked + .circle {
  border-color: var(--radio-selected-border);
  background: var(--radio-selected-gradient);
}
.radio input:checked + .circle .dot {
  background: var(--radio-dot-color);
  box-shadow: var(--radio-dot-shadow);
}
.radio {
  /* Focus state */
}
.radio input:focus + .circle {
  box-shadow: var(--radio-focus-ring);
}
.radio {
  /* Disabled state */
}
.radio.disabled, .radio:has(input:disabled) {
  opacity: 0.6;
  cursor: default;
  pointer-events: none;
}

/* Native radio fallback (element-based) */
input[type=radio]:not(.radio input) {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: var(--radio-size);
  height: var(--radio-size);
  border-radius: 50%;
  border: 0.5px solid var(--radio-border);
  background: var(--radio-gradient);
  cursor: default;
  vertical-align: middle;
  position: relative;
  transition: all 0.15s ease;
  overflow: hidden;
  /* Aqua glass effect (shine only - ::after used for dot) */
}
input[type=radio]:not(.radio input)::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 40%;
  width: 75%;
  top: 8%;
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
  border-radius: 50% 50% 30% 30%;
  filter: blur(0.5px);
  z-index: 2;
  pointer-events: none;
}
input[type=radio]:not(.radio input) {
  /* Inner dot */
}
input[type=radio]:not(.radio input)::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: transparent;
  transform: translate(-50%, -50%);
  transition: background 0.1s ease;
}
input[type=radio]:not(.radio input):checked {
  border-color: var(--radio-selected-border);
  background: var(--radio-selected-gradient);
}
input[type=radio]:not(.radio input):checked::after {
  background: var(--radio-dot-color);
  box-shadow: var(--radio-dot-shadow);
}
input[type=radio]:not(.radio input):focus {
  box-shadow: var(--radio-focus-ring);
  outline: none;
}
input[type=radio]:not(.radio input):active:not(:disabled) {
  background: var(--radio-pressed-gradient);
}
input[type=radio]:not(.radio input):disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Radio with label wrapper */
.field-row input[type=radio] + label,
label input[type=radio] {
  cursor: pointer;
}

.field-row:has(input[type=radio]:disabled) label,
label:has(input[type=radio]:disabled) {
  color: var(--text-disabled);
  cursor: not-allowed;
}

/**
 * Switch / Toggle
 * Mac OS X Aqua-style toggle switch
 */
.aqua-switch {
  position: relative;
  border-radius: 9999px;
