  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5));
  border-radius: inherit;
  filter: blur(3px);
  z-index: 1;
