/**
 * Scrollbars
 * Mac OS X Aqua-style custom scrollbars with translucent blue thumbs
 */
::-webkit-scrollbar {
  width: 15px;
  height: 15px;
}

::-webkit-scrollbar-track {
  border-radius: 8px;
  background: var(--scrollbar-track-gradient);
  box-shadow: var(--scrollbar-track-shadow);
}

::-webkit-scrollbar-track:horizontal {
  background: var(--scrollbar-track-gradient-horizontal);
}

/* Aqua blue translucent thumb */
::-webkit-scrollbar-thumb {
  border-radius: 8px;
  min-height: 30px;
  min-width: 30px;
  /* Aqua blue translucent gradient */
  background: var(--scrollbar-thumb-gradient);
  box-shadow: var(--scrollbar-thumb-shadow);
}

::-webkit-scrollbar-thumb:horizontal {
  background: var(--scrollbar-thumb-gradient-horizontal);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--scrollbar-thumb-hover-gradient);
  box-shadow: var(--scrollbar-thumb-hover-shadow);
}

::-webkit-scrollbar-thumb:horizontal:hover {
  background: var(--scrollbar-thumb-hover-gradient-horizontal);
}

::-webkit-scrollbar-thumb:active {
  background: var(--scrollbar-thumb-active-gradient);
  box-shadow: var(--scrollbar-thumb-active-shadow);
}

::-webkit-scrollbar-thumb:horizontal:active {
  background: var(--scrollbar-thumb-active-gradient-horizontal);
}

/* Corner piece when both scrollbars are visible */
::-webkit-scrollbar-corner {
  background: var(--scrollbar-corner-gradient);
  box-shadow: var(--scrollbar-corner-shadow);
}

/* Firefox scrollbar support */
* {
  scrollbar-width: thin;
  scrollbar-color: var(--scrollbar-firefox-thumb) var(--scrollbar-firefox-track);
}

/* ============================================
   OPTIONAL OVERLAY SCROLLBAR (scoped)
   ============================================ */
.scroll-container {
  overflow: auto;
