  /* Pinstripe pattern overlay */
  background-image: var(--menu-bar-pinstripe), var(--menu-bar-gradient);
  /* Border and shadow */
  border-bottom: 1px solid var(--menu-bar-border);
  box-shadow: var(--menu-bar-shadow);
  position: relative;
  /* Top gloss effect */
}
.menu-bar::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
