/* ============================================
   CONTEXT MENU (Right-click menu)
   JavaFX AquaFX accurate
   ============================================ */
.context-menu,
.aqua-menu {
  background: var(--context-menu-bg);
  border: 0.5px solid var(--context-menu-border);
  border-radius: 6px;
