}

.aqua-dialog--medium {
  width: 403px;
}

.aqua-dialog--large {
  width: 550px;
}

.aqua-dialog--full {
  width: 100%;
}

/* Dialog sections */
.aqua-dialog__header {
  font-size: 11pt;
  font-weight: bold;
  margin-bottom: 14px;
  text-align: center;
}

.aqua-dialog__content {
  margin-bottom: 14px;
}

.aqua-dialog__section {
  background: var(--dialog-section-bg);
  padding: 14px;
  text-align: center;
  border-radius: 4px;
  box-shadow: var(--dialog-section-shadow);
  font-size: 10pt;
  margin: 10px 0;
}

.aqua-dialog__section-description {
  margin-top: 5px;
  font-size: 7.7pt;
  color: var(--dialog-section-description);
}

.aqua-dialog__row {
  font-size: 10pt;
  text-align: left;
  margin-top: 10px;
  margin-left: 6px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.aqua-dialog__row:first-child {
  margin-top: 14px;
}

.aqua-dialog__row label {
  display: inline-block;
  min-width: 70px;
  text-align: right;
}

.aqua-dialog__footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 14px;
  margin-right: 7px;
}

/* Animation for slide in/out */
.aqua-dialog--animated {
  transition-timing-function: linear;
  transition-duration: 0.25s;
}

/* Alert dialog variant */
.alert-dialog {
  background: var(--alert-dialog-bg);
  padding: 16px 20px;
  border-radius: 10px;
  max-width: 400px;
  font-family: "Lucida Grande", "Lucida Sans Unicode", sans-serif;
