}

.alert-dialog-content {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
}

.alert-dialog-icon {
  flex-shrink: 0;
  width: 48px;
  height: 48px;
  border-radius: 8px;
}
.alert-dialog-icon.warning {
  background: var(--alert-dialog-icon-warning-gradient);
  box-shadow: var(--alert-dialog-icon-shadow);
}
.alert-dialog-icon.error {
  background: var(--alert-dialog-icon-error-gradient);
  box-shadow: var(--alert-dialog-icon-shadow);
}
.alert-dialog-icon.info {
  background: var(--alert-dialog-icon-info-gradient);
  box-shadow: var(--alert-dialog-icon-shadow);
}
.alert-dialog-icon.success {
  background: var(--alert-dialog-icon-success-gradient);
  box-shadow: var(--alert-dialog-icon-shadow);
}

.alert-dialog-text {
  flex: 1;
}
.alert-dialog-text h3 {
  margin: 0 0 8px 0;
  font-size: 13px;
  font-weight: 600;
  color: var(--alert-dialog-title);
}
.alert-dialog-text p {
  margin: 0;
  font-size: 12px;
  color: var(--alert-dialog-text);
  line-height: 1.4;
}

.alert-dialog-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
}

