/**
 * Table View
 * Mac OS X Aqua-style table - JavaFX AquaFX accurate
 */
.table-view,
table.aqua,
table.aqua-table {
  width: 100%;
  border-collapse: collapse;
  background: var(--table-bg);
  border: 1px solid;
  border-color: var(--table-border);
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  /* Column headers */
}
.table-view th,
table.aqua th,
table.aqua-table th {
  background: var(--table-header-gradient);
  padding: 0 10px;
  text-align: left;
  font-weight: normal;
  border-bottom: 0.5px solid var(--table-header-border-bottom);
  border-right: 1px solid var(--table-header-border-right);
  height: 22px;
  position: relative;
  /* Aqua glass effect */
}
.table-view th::before,
table.aqua th::before,
table.aqua-table th::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 50%;
  width: 100%;
  top: 0;
  background: linear-gradient(rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.05));
  border-radius: 0;
  z-index: 1;
  pointer-events: none;
}
.table-view th::after,
table.aqua th::after,
table.aqua-table th::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 30%;
  width: 100%;
  bottom: 0;
  background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.2));
  border-radius: 0;
  z-index: 1;
  pointer-events: none;
}
.table-view th:last-child,
table.aqua th:last-child,
table.aqua-table th:last-child {
  border-right: none;
}
.table-view,
table.aqua,
table.aqua-table {
  /* Table cells */
}
.table-view td,
table.aqua td,
table.aqua-table td {
  padding: 0 10px;
  border-bottom: 1px solid var(--table-cell-border);
  height: 22px;
}
.table-view,
table.aqua,
table.aqua-table {
  /* Alternating row colors */
}
.table-view tr:nth-child(odd) td,
table.aqua tr:nth-child(odd) td,
table.aqua-table tr:nth-child(odd) td {
  background: var(--table-row-odd);
}
.table-view tr:nth-child(even) td,
table.aqua tr:nth-child(even) td,
table.aqua-table tr:nth-child(even) td {
  background: var(--table-row-even);
}
.table-view tr:last-child td,
table.aqua tr:last-child td,
table.aqua-table tr:last-child td {
  border-bottom: none;
}
.table-view,
table.aqua,
table.aqua-table {
  /* Hover state */
}
.table-view tr:hover td,
table.aqua tr:hover td,
table.aqua-table tr:hover td {
  background: var(--table-row-hover);
}
.table-view,
table.aqua,
table.aqua-table {
  /* Selected row */
}
.table-view tr.selected td,
table.aqua tr.selected td,
table.aqua-table tr.selected td {
  background: var(--table-row-selected);
  color: var(--table-row-selected-text);
}
.table-view,
table.aqua,
table.aqua-table {
  /* Interactive table */
}
.table-view.interactive tbody tr,
table.aqua.interactive tbody tr,
table.aqua-table.interactive tbody tr {
  cursor: pointer;
}

/* Standard table styling (legacy) */
table:not(.aqua):not(.table-view) {
  border-collapse: collapse;
  font-size: var(--font-size-base);
  background: var(--table-bg);
  text-align: left;
