  padding: 4px 12px;
  border-bottom: 1px solid var(--table-legacy-row-border);
}

/**
 * List View
 * Mac OS X Aqua-style list - JavaFX AquaFX accurate
 */
/* ============================================
   VERTICAL LIST (Default)
   ============================================ */
.list-view,
.aqua-list {
  background: var(--list-bg);
  border: 1px solid;
  border-color: var(--list-border);
  overflow: auto;
  min-width: 150px;
  max-height: 200px;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  /* Focus ring */
}
.list-view:focus-within, .list-view.focused,
.aqua-list:focus-within,
.aqua-list.focused {
  box-shadow: var(--list-focus-ring);
}
.list-view,
.aqua-list {
  /* Disabled state */
}
.list-view.disabled,
.aqua-list.disabled {
  opacity: 0.5;
  pointer-events: none;
}

/* List item/cell */
.list-item,
.aqua-list-item {
  padding: 3px 3px;
  cursor: pointer;
  background: var(--list-item-bg);
  color: var(--list-item-color);
  /* Hover state */
}
.list-item:hover,
.aqua-list-item:hover {
  background: var(--list-item-hover-bg);
}
.list-item,
.aqua-list-item {
  /* Selected state when list is NOT focused (gray selection) */
}
.list-item.selected,
.aqua-list-item.selected {
  background: var(--list-item-selected-strip-gradient), var(--list-item-selected-fill-gradient);
  background-size: 100% 1px, 100% 100%;
  background-position: 0 0, 0 1px;
  background-repeat: no-repeat;
  color: var(--list-item-selected-text);
}

/* When list container is focused, selected items get blue gradient */
.list-view:focus-within .list-item.selected,
.list-view.focused .list-item.selected,
.aqua-list:focus-within .aqua-list-item.selected,
.aqua-list.focused .aqua-list-item.selected {
  background: var(--list-item-selected-focused-strip-gradient), var(--list-item-selected-focused-fill-gradient);
  background-size: 100% 1px, 100% 100%;
  background-position: 0 0, 0 1px;
  background-repeat: no-repeat;
  color: var(--list-item-selected-focused-text);
  text-shadow: var(--list-item-selected-focused-text-shadow);
}

/* ============================================
   HORIZONTAL LIST
   ============================================ */
.list-view.horizontal,
.aqua-list.horizontal {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
  overflow-y: hidden;
  max-height: none;
  height: auto;
}
.list-view.horizontal .list-item,
.list-view.horizontal .aqua-list-item,
.aqua-list.horizontal .list-item,
.aqua-list.horizontal .aqua-list-item {
  flex-shrink: 0;
  white-space: nowrap;
  /* Odd items in horizontal list */
}
.list-view.horizontal .list-item:nth-child(odd),
.list-view.horizontal .aqua-list-item:nth-child(odd),
.aqua-list.horizontal .list-item:nth-child(odd),
.aqua-list.horizontal .aqua-list-item:nth-child(odd) {
  background: var(--list-item-odd-bg);
}
.list-view.horizontal .list-item:hover,
.list-view.horizontal .aqua-list-item:hover,
.aqua-list.horizontal .list-item:hover,
.aqua-list.horizontal .aqua-list-item:hover {
  background: var(--list-item-hover-bg);
}
.list-view.horizontal .list-item,
.list-view.horizontal .aqua-list-item,
.aqua-list.horizontal .list-item,
.aqua-list.horizontal .aqua-list-item {
  /* Selected state (unfocused) - horizontal gradient */
}
.list-view.horizontal .list-item.selected,
.list-view.horizontal .aqua-list-item.selected,
.aqua-list.horizontal .list-item.selected,
.aqua-list.horizontal .aqua-list-item.selected {
  background: var(--list-item-selected-strip-gradient-horizontal), var(--list-item-selected-fill-gradient-horizontal);
  background-size: 1px 100%, 100% 100%;
  background-position: 0 0, 1px 0;
  background-repeat: no-repeat;
  color: var(--list-item-selected-text);
}

/* Horizontal list focused selection */
.list-view.horizontal:focus-within .list-item.selected,
.list-view.horizontal.focused .list-item.selected,
.aqua-list.horizontal:focus-within .aqua-list-item.selected,
.aqua-list.horizontal.focused .aqua-list-item.selected {
  background: var(--list-item-selected-focused-strip-gradient-horizontal), var(--list-item-selected-focused-fill-gradient-horizontal);
  background-size: 1px 100%, 100% 100%;
  background-position: 0 0, 1px 0;
  background-repeat: no-repeat;
  color: var(--list-item-selected-focused-text);
  text-shadow: var(--list-item-selected-focused-text-shadow);
}

/* ============================================
   STRIPED LIST (Alternating rows)
   ============================================ */
.list-view.striped .list-item:nth-child(odd),
.list-view.striped .aqua-list-item:nth-child(odd),
.aqua-list.striped .list-item:nth-child(odd),
.aqua-list.striped .aqua-list-item:nth-child(odd) {
  background: var(--list-item-odd-bg);
}
.list-view.striped .list-item:nth-child(even),
.list-view.striped .aqua-list-item:nth-child(even),
.aqua-list.striped .list-item:nth-child(even),
.aqua-list.striped .aqua-list-item:nth-child(even) {
  background: var(--list-item-even-bg);
}
.list-view.striped .list-item:hover,
.list-view.striped .aqua-list-item:hover,
.aqua-list.striped .list-item:hover,
.aqua-list.striped .aqua-list-item:hover {
  background: var(--list-item-hover-bg);
}

/**
 * Listbox
 * Multi-select list using select[multiple] or role="listbox"
 */
[role=listbox],
select[multiple] {
  background: var(--listbox-bg);
  border: 1px solid var(--listbox-border);
  border-radius: var(--radius-small);
  display: block;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  overflow-y: auto;
  box-shadow: var(--inset-shadow);
  padding: 2px;
  min-height: 80px;
}
[role=listbox]:focus,
select[multiple]:focus {
  border-color: var(--aqua-blue);
  box-shadow: var(--inset-shadow), var(--listbox-focus-shadow);
  outline: none;
}
[role=listbox] [role=option],
[role=listbox] option,
select[multiple] [role=option],
select[multiple] option {
  padding: 4px 8px;
  cursor: default;
  border-radius: var(--radius-small);
}
[role=listbox] [role=option][aria-selected=true], [role=listbox] [role=option]:checked, [role=listbox] [role=option]:focus,
[role=listbox] option[aria-selected=true],
[role=listbox] option:checked,
[role=listbox] option:focus,
select[multiple] [role=option][aria-selected=true],
select[multiple] [role=option]:checked,
select[multiple] [role=option]:focus,
select[multiple] option[aria-selected=true],
select[multiple] option:checked,
select[multiple] option:focus {
  background: var(--listbox-option-selected-bg);
  color: var(--listbox-option-selected-text);
}
[role=listbox] [role=option]:hover:not([aria-selected=true]):not(:checked),
[role=listbox] option:hover:not([aria-selected=true]):not(:checked),
select[multiple] [role=option]:hover:not([aria-selected=true]):not(:checked),
select[multiple] option:hover:not([aria-selected=true]):not(:checked) {
  background: var(--listbox-option-hover-bg);
}

/**
 * Tree View
 * Mac OS X Aqua-style tree view - JavaFX AquaFX accurate
 */
/* ============================================
   TREE VIEW CONTAINER
   ============================================ */
.tree-view,
.aqua-tree {
  background: var(--tree-bg);
  border: 1px solid;
  border-color: var(--tree-border);
  padding: 0;
  min-width: 200px;
  overflow: auto;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  /* Focus ring */
}
.tree-view:focus-within, .tree-view.focused,
.aqua-tree:focus-within,
.aqua-tree.focused {
  box-shadow: var(--tree-focus-ring);
}
.tree-view,
.aqua-tree {
  /* Nested lists */
}
.tree-view ul,
.aqua-tree ul {
  list-style: none;
  margin: 0;
  padding-left: 16px;
}

/* ============================================
   TREE ITEM
   ============================================ */
.tree-item,
.aqua-tree-item {
  padding: 1px 3px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 4px;
}
.tree-item:hover,
.aqua-tree-item:hover {
  background: var(--tree-item-hover-bg);
}
.tree-item,
.aqua-tree-item {
  /* Selected state (unfocused - gray) */
}
.tree-item.selected,
.aqua-tree-item.selected {
  background: var(--tree-item-selected-strip-gradient), var(--tree-item-selected-fill-gradient);
  background-size: 100% 1px, 100% 100%;
  background-position: 0 0, 0 1px;
  background-repeat: no-repeat;
  color: var(--tree-item-selected-text);
}

/* Focused tree selected items get blue gradient */
.tree-view:focus-within .tree-item.selected,
.tree-view.focused .tree-item.selected,
.aqua-tree:focus-within .aqua-tree-item.selected,
.aqua-tree.focused .aqua-tree-item.selected {
  background: var(--tree-item-selected-focused-strip-gradient), var(--tree-item-selected-focused-fill-gradient);
  background-size: 100% 1px, 100% 100%;
  background-position: 0 0, 0 1px;
  background-repeat: no-repeat;
  color: var(--tree-item-selected-focused-text);
  text-shadow: var(--tree-item-selected-focused-text-shadow);
}

/* ============================================
   DISCLOSURE ARROW
   ============================================ */
.tree-arrow,
.aqua-tree-arrow {
  width: 0;
  height: 0;
  border-left: 4px solid var(--tree-arrow-color);
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
  margin-left: 4px;
  transition: transform 0.1s;
  flex-shrink: 0;
}

/* Expanded state */
.tree-item.expanded .tree-arrow,
.aqua-tree-item.expanded .aqua-tree-arrow {
  transform: rotate(90deg);
}

/* ============================================
   TREE NODE ICON
   ============================================ */
.tree-icon {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}

/* ============================================
   TREE USING HTML5 DETAILS/SUMMARY
   ============================================ */
.tree-view details > summary {
  list-style: none;
  cursor: pointer;
  padding: 1px 3px;
  display: flex;
  align-items: center;
  gap: 4px;
}
.tree-view details > summary::-webkit-details-marker {
  display: none;
}
.tree-view details > summary::before {
  content: "";
  width: 0;
  height: 0;
  border-left: 4px solid var(--tree-arrow-color);
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
  transition: transform 0.1s;
  flex-shrink: 0;
}
.tree-view details > summary:hover {
  background: var(--tree-item-hover-bg);
}
.tree-view details > summary.selected {
  background: var(--tree-item-selected-solid-bg);
  color: var(--tree-item-selected-solid-text);
}
.tree-view details[open] > summary::before {
  transform: rotate(90deg);
}
.tree-view details > ul {
  padding-left: 16px;
}
.tree-view {
  /* Plain list items */
}
.tree-view li:not(details):not(summary) {
  padding: 1px 3px 1px 12px;
  cursor: pointer;
}
.tree-view li:not(details):not(summary):hover {
  background: var(--tree-item-hover-bg);
}
.tree-view li:not(details):not(summary).selected {
  background: var(--tree-item-selected-solid-bg);
  color: var(--tree-item-selected-solid-text);
}

/* ============================================
   TREE VIEW DISABLED
   ============================================ */
.tree-view.disabled,
.aqua-tree.disabled {
  opacity: 0.5;
  pointer-events: none;
}

/**
