.tree-table thead th,
table.aqua-tree-table thead th {
  /* Sort arrow */
}
.tree-table thead th .sort-arrow,
table.aqua-tree-table thead th .sort-arrow {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 4px;
  vertical-align: middle;
}
.tree-table thead th .sort-arrow.asc,
table.aqua-tree-table thead th .sort-arrow.asc {
  border-left: 3.5px solid transparent;
  border-right: 3.5px solid transparent;
  border-bottom: 4px solid var(--table-sort-arrow);
}
.tree-table thead th .sort-arrow.desc,
table.aqua-tree-table thead th .sort-arrow.desc {
  border-left: 3.5px solid transparent;
  border-right: 3.5px solid transparent;
  border-top: 4px solid var(--table-sort-arrow);
}
.tree-table,
table.aqua-tree-table {
  /* Table rows */
}
.tree-table tbody tr,
table.aqua-tree-table tbody tr {
  background: var(--table-row-odd);
}
.tree-table tbody tr:nth-child(odd),
table.aqua-tree-table tbody tr:nth-child(odd) {
  background: var(--table-row-even);
}
.tree-table tbody tr:hover,
table.aqua-tree-table tbody tr:hover {
  background: var(--table-row-hover);
}
.tree-table tbody tr,
table.aqua-tree-table tbody tr {
  /* Selected (unfocused) - gray */
}
.tree-table tbody tr.selected,
table.aqua-tree-table tbody tr.selected {
  background: var(--tree-item-selected-strip-gradient), var(--tree-item-selected-fill-gradient);
  background-size: 100% 1px, 100% 100%;
  background-position: 0 0, 0 1px;
  background-repeat: no-repeat;
  color: var(--tree-item-selected-text);
}
.tree-table,
table.aqua-tree-table {
  /* Table cells */
}
.tree-table tbody td,
table.aqua-tree-table tbody td {
  padding: 3px 8px;
  border: none;
  vertical-align: middle;
  height: 22px;
}
.tree-table,
table.aqua-tree-table {
  /* Focus state */
}
.tree-table:focus, .tree-table:focus-within,
table.aqua-tree-table:focus,
table.aqua-tree-table:focus-within {
  box-shadow: var(--tree-focus-ring);
}
.tree-table:focus tbody tr.selected, .tree-table:focus-within tbody tr.selected,
table.aqua-tree-table:focus tbody tr.selected,
table.aqua-tree-table:focus-within tbody tr.selected {
  background: var(--tree-item-selected-focused-strip-gradient), var(--tree-item-selected-focused-fill-gradient);
  background-size: 100% 1px, 100% 100%;
  background-position: 0 0, 0 1px;
  background-repeat: no-repeat;
  color: var(--tree-item-selected-focused-text);
  text-shadow: var(--tree-item-selected-focused-text-shadow);
}
.tree-table,
table.aqua-tree-table {
  /* Disabled */
}
.tree-table.disabled,
table.aqua-tree-table.disabled {
  opacity: 0.5;
  pointer-events: none;
}

/* ============================================
   TREE CELL (use inside td for proper alignment)
   ============================================ */
.tree-cell,
.aqua-tree-cell {
  display: flex;
  align-items: center;
  gap: 2px;
}

/* ============================================
   TREE DISCLOSURE ARROW
   ============================================ */
.tree-table-arrow,
.aqua-tree-table-arrow {
  display: inline-block;
  width: 8px;
  height: 8px;
  margin-right: 4px;
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
  /* Arrow triangle using borders */
}
.tree-table-arrow::before,
.aqua-tree-table-arrow::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-left: 6px solid var(--tree-arrow-color);
  transition: transform 0.15s ease;
}
.tree-table-arrow.expanded::before,
.aqua-tree-table-arrow.expanded::before {
  transform: rotate(90deg);
  transform-origin: 2px 4px;
}
.tree-table-arrow,
.aqua-tree-table-arrow {
  /* Hidden placeholder for leaf nodes */
}
.tree-table-arrow.leaf::before,
.aqua-tree-table-arrow.leaf::before {
  visibility: hidden;
}

/* ============================================
   INDENTATION SPACER
   ============================================ */
.tree-table-indent,
.aqua-tree-table-indent {
  display: inline-block;
  width: 16px;
  flex-shrink: 0;
}

/**
 * Segmented Control
 * Button group for view switching
 */
.segmented-control {
  display: inline-flex;
  border: 1px solid var(--segmented-border);
  border-radius: var(--radius-medium);
  overflow: hidden;
}
.segmented-control button {
  border: none;
  border-radius: 0;
  border-right: 1px solid var(--segmented-divider);
  min-width: 60px;
  min-height: 24px;
  box-shadow: none;
}
.segmented-control button:last-child {
  border-right: none;
}
.segmented-control button.active, .segmented-control button[aria-pressed=true] {
  background: var(--segmented-active-bg);
  color: var(--segmented-active-text);
  text-shadow: var(--segmented-active-text-shadow);
}

/**
 * Tooltip
 * Mac OS X Aqua-style tooltips - JavaFX AquaFX accurate
 * Supports both data-attribute and nested element approaches
 */
/* Data-attribute tooltip (convenient) */
.tooltip,
[data-tooltip] {
  position: relative;
}
.tooltip::after,
[data-tooltip]::after {
  content: attr(data-tooltip);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  padding: 1px 5px 3px 3px;
  background: var(--tooltip-bg);
  border: 0.5px solid var(--tooltip-border);
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  color: var(--tooltip-text);
  white-space: nowrap;
  box-shadow: var(--tooltip-shadow);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.15s, visibility 0.15s;
  pointer-events: none;
  z-index: 1000;
  margin-bottom: 6px;
}
.tooltip:hover::after,
[data-tooltip]:hover::after {
  opacity: 1;
  visibility: visible;
}

/* Class-based tooltip with nested element */
.tooltip-wrapper,
.aqua-tooltip {
  position: relative;
  display: inline-block;
}
.tooltip-wrapper .tooltip-text,
.aqua-tooltip .tooltip-text {
  visibility: hidden;
  opacity: 0;
  background: var(--tooltip-bg);
  color: var(--tooltip-text);
  padding: 1px 5px 3px 3px;
  border: 0.5px solid var(--tooltip-border);
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  position: absolute;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  box-shadow: var(--tooltip-shadow);
  z-index: 1000;
  transition: opacity 0.15s, visibility 0.15s;
}
.tooltip-wrapper:hover .tooltip-text,
.aqua-tooltip:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

/* Tooltip positions */
.tooltip.top::after,
[data-tooltip].top::after {
  bottom: 100%;
  top: auto;
  margin-bottom: 6px;
}

.tooltip.bottom::after,
[data-tooltip].bottom::after {
  bottom: auto;
  top: 100%;
  margin-top: 6px;
  margin-bottom: 0;
}

.tooltip.left::after,
[data-tooltip].left::after {
  left: auto;
  right: 100%;
  bottom: auto;
  top: 50%;
  transform: translateY(-50%);
  margin-right: 6px;
  margin-bottom: 0;
}

.tooltip.right::after,
[data-tooltip].right::after {
  left: 100%;
  right: auto;
  bottom: auto;
  top: 50%;
  transform: translateY(-50%);
  margin-left: 6px;
  margin-bottom: 0;
}

/**
 * Balloon / Speech Bubble
 * Tooltip with a speech bubble tail
 */
.balloon,
[role=tooltip].balloon {
  position: relative;
  padding: 12px 16px;
  background: var(--balloon-gradient);
  border: 1px solid var(--balloon-border);
  border-radius: var(--radius-medium);
  box-shadow: var(--balloon-shadow);
  font-size: var(--font-size-base);
  /* Tail pointing up (default) */
}
.balloon::before,
[role=tooltip].balloon::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid var(--balloon-border);
  top: -9px;
  left: 16px;
}
.balloon::after,
[role=tooltip].balloon::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-bottom: 7px solid var(--balloon-tail-bg);
  top: -7px;
  left: 17px;
}
.balloon,
[role=tooltip].balloon {
  /* Tail pointing down */
}
.balloon.balloon-bottom::before,
[role=tooltip].balloon.balloon-bottom::before {
  border-bottom: none;
  border-top: 8px solid var(--balloon-border);
  top: auto;
  bottom: -9px;
}
.balloon.balloon-bottom::after,
[role=tooltip].balloon.balloon-bottom::after {
  border-bottom: none;
  border-top: 7px solid var(--balloon-tail-bg-alt);
  top: auto;
  bottom: -7px;
}
.balloon,
[role=tooltip].balloon {
  /* Tail on the right side */
}
.balloon.balloon-right::before, .balloon.balloon-right::after,
[role=tooltip].balloon.balloon-right::before,
[role=tooltip].balloon.balloon-right::after {
  left: auto;
  right: 16px;
}
.balloon.balloon-right::after,
[role=tooltip].balloon.balloon-right::after {
  right: 17px;
}
.balloon,
[role=tooltip].balloon {
  /* Tail pointing left */
}
.balloon.balloon-left-tail::before,
[role=tooltip].balloon.balloon-left-tail::before {
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-left: none;
  border-right: 8px solid var(--balloon-border);
  top: 12px;
  left: -9px;
}
.balloon.balloon-left-tail::after,
[role=tooltip].balloon.balloon-left-tail::after {
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: none;
  border-right: 7px solid var(--balloon-tail-bg);
  top: 13px;
  left: -7px;
}
.balloon,
[role=tooltip].balloon {
  /* Tail pointing right */
}
.balloon.balloon-right-tail::before,
[role=tooltip].balloon.balloon-right-tail::before {
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-right: none;
  border-left: 8px solid var(--balloon-border);
  top: 12px;
  left: auto;
  right: -9px;
}
.balloon.balloon-right-tail::after,
[role=tooltip].balloon.balloon-right-tail::after {
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-right: none;
  border-left: 7px solid var(--balloon-tail-bg-alt);
  top: 13px;
  left: auto;
  right: -7px;
}

/**
 * Stepper / Number Input
 * Number input with increment/decrement buttons
 */
.stepper {
  display: inline-flex;
  align-items: stretch;
  border: 1px solid var(--stepper-border);
  border-radius: 4px;
  overflow: hidden;
  background: var(--stepper-bg);
}
.stepper input[type=number] {
  border: none;
  border-radius: 0;
  box-shadow: none;
  width: 50px;
  height: auto;
  padding: 4px 8px;
  text-align: center;
  background: var(--stepper-bg);
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  -moz-appearance: textfield;
}
.stepper input[type=number]::-webkit-inner-spin-button, .stepper input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.stepper input[type=number]:focus {
  outline: none;
}
.stepper button {
  min-width: 28px;
  padding: 4px 8px;
  border: none;
  border-radius: 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  background: var(--stepper-button-gradient);
  cursor: default;
  color: var(--stepper-button-text);
  position: relative;
  overflow: hidden;
  /* Aqua glass effect */
}
.stepper button::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  top: 5%;
  background: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.3));
  border-radius: inherit;
  filter: blur(1px);
  z-index: 1;
  pointer-events: none;
}
.stepper button::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5));
  border-radius: inherit;
  filter: blur(3px);
  z-index: 1;
  pointer-events: none;
}
.stepper button:first-child {
  border-right: 1px solid var(--stepper-button-divider);
}
.stepper button:last-child {
  border-left: 1px solid var(--stepper-button-divider);
}
.stepper button:hover {
  background: var(--stepper-button-hover-gradient);
}
.stepper button:active {
  background: var(--stepper-button-active-gradient);
}

/* ============================================
   Aqua Spinner / Stepper (JavaFX-style)
   ============================================ */
.aqua-spinner {
  display: inline-flex;
  align-items: stretch;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
}
.aqua-spinner input {
  width: 60px;
  height: auto;
  padding: 4px 8px;
  text-align: center;
  border: 1px solid var(--stepper-spinner-border);
  border-right: none;
  border-radius: 3px 0 0 3px;
  outline: none;
  background: var(--stepper-bg);
  box-shadow: var(--stepper-spinner-shadow);
}
.aqua-spinner input::-webkit-inner-spin-button, .aqua-spinner input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.aqua-spinner input {
  -moz-appearance: textfield;
}
.aqua-spinner input:focus {
  border-color: var(--stepper-spinner-focus-border);
  box-shadow: var(--stepper-spinner-focus-shadow);
}

.aqua-spinner-buttons {
  display: flex;
  flex-direction: column;
}
.aqua-spinner-buttons button {
  font-size: 8px;
  padding: 0 6px;
  min-width: 18px;
  border: 1px solid var(--stepper-spinner-border);
  border-left: none;
  background: var(--stepper-spinner-button-gradient);
  cursor: default;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  color: var(--stepper-button-text);
  position: relative;
  overflow: hidden;
  /* Aqua glass effect */
}
.aqua-spinner-buttons button::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  top: 5%;
  background: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.3));
  border-radius: inherit;
  filter: blur(1px);
  z-index: 1;
  pointer-events: none;
}
.aqua-spinner-buttons button::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5));
  border-radius: inherit;
  filter: blur(3px);
  z-index: 1;
  pointer-events: none;
}
.aqua-spinner-buttons button:first-child {
  border-radius: 0 3px 0 0;
  border-bottom: none;
}
.aqua-spinner-buttons button:last-child {
  border-radius: 0 0 3px 0;
}
.aqua-spinner-buttons button:active {
  background: var(--stepper-spinner-button-active-gradient);
}
.aqua-spinner-buttons button:hover {
  background: var(--stepper-spinner-button-hover-gradient);
}
.aqua-spinner-buttons button:active {
  background: var(--stepper-button-active-gradient);
}

/**
 * Combobox
 * Text input with dropdown button
 */
.combobox {
  position: relative;
  display: inline-flex;
  align-items: stretch;
}
.combobox input[type=text] {
  padding-right: 28px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: none;
  flex: 1;
}
.combobox button {
  min-width: 20px;
  padding: 0 4px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.combobox button::after {
  content: "";
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 5px solid var(--text-color);
}
.combobox button:hover::after {
  border-top-color: var(--text-color);
}
.combobox button:disabled::after {
  border-top-color: var(--text-disabled);
}
.combobox {
  /* Dropdown list */
}
.combobox .combobox-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: var(--combobox-dropdown-bg);
  border: 1px solid var(--combobox-dropdown-border);
  border-top: none;
  border-radius: 0 0 var(--radius-small) var(--radius-small);
  box-shadow: var(--combobox-dropdown-shadow);
  max-height: 150px;
  overflow-y: auto;
  z-index: 100;
  display: none;
}
.combobox .combobox-dropdown.open {
  display: block;
}
.combobox .combobox-dropdown .combobox-option {
  padding: 4px 8px;
  cursor: default;
}
.combobox .combobox-dropdown .combobox-option:hover {
  background: var(--combobox-option-hover-bg);
  color: var(--combobox-option-hover-text);
}
.combobox .combobox-dropdown .combobox-option.selected {
  background: var(--combobox-option-hover-bg);
  color: var(--combobox-option-hover-text);
}

/**
 * Spinner / Loader
 * Animated loading indicator
 */
.spinner,
.loader {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 2px solid var(--spinner-border);
  border-top-color: var(--aqua-blue);
  border-radius: 50%;
}
.spinner.animate, .spinner.spinning,
.loader.animate,
.loader.spinning {
  animation: spinner-rotate 0.8s linear infinite;
}

/* Large spinner */
.spinner-large,
.loader-large {
  width: 32px;
  height: 32px;
  border-width: 3px;
}

/* Small spinner */
.spinner-small,
.loader-small {
  width: 14px;
  height: 14px;
  border-width: 2px;
}

@keyframes spinner-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* Aqua-style "beach ball" spinner */
.spinner-aqua {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: var(--spinner-aqua-gradient);
}
.spinner-aqua.animate, .spinner-aqua.spinning {
  animation: spinner-rotate 1s linear infinite;
}

/* Pinwheel Spinner */
.spinner-container {
  --blade-opacity: 0.25;
  position: relative;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  box-shadow: var(--spinner-container-shadow);
  display: inline-block;
  vertical-align: middle;
  /* Aqua glass effect (top shine + bottom glow) */
}
.spinner-container::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 40%;
  width: 75%;
  top: 8%;
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
  border-radius: 50% 50% 30% 30%;
  filter: blur(0.5px);
  z-index: 2;
  pointer-events: none;
}
.spinner-container::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 35%;
  width: 70%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.35));
  border-radius: 30% 30% 50% 50%;
  filter: blur(1px);
  pointer-events: none;
}

/* Rainbow background layer */
.static-rainbow-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.bg-smooth-rainbow {
  background: var(--spinner-rainbow-gradient);
}

/* Spinning pinwheel layer */
.spinning-pinwheel {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  animation: spinner-rotate 1.2s linear infinite;
  z-index: 10;
}
.spinning-pinwheel .blade {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  clip-path: url(#spinnerBlade);
  transform-origin: 50% 50%;
  background: var(--spinner-blade-color);
}
.spinning-pinwheel .blade:nth-child(1) {
  transform: rotate(0deg);
}
.spinning-pinwheel .blade:nth-child(2) {
  transform: rotate(72deg);
}
.spinning-pinwheel .blade:nth-child(3) {
  transform: rotate(144deg);
}
.spinning-pinwheel .blade:nth-child(4) {
  transform: rotate(216deg);
}
.spinning-pinwheel .blade:nth-child(5) {
  transform: rotate(288deg);
}

/* Static mode - no animation */
.spinner-container.static-mode .spinning-pinwheel {
  animation: none;
}

/**
 * Split Pane
 * Mac OS X Aqua-style split pane - JavaFX AquaFX accurate
 */
/* ============================================
   SPLIT PANE CONTAINER
   ============================================ */
.split-pane,
.aqua-split-pane {
  display: flex;
  flex-direction: row;
  border: 1px solid var(--split-pane-border);
  background: var(--split-pane-bg);
  overflow: hidden;
  /* Child panes */
}
.split-pane > div:not(.split-divider):not(.aqua-split-divider),
.aqua-split-pane > div:not(.split-divider):not(.aqua-split-divider) {
  overflow: auto;
  min-width: 0;
  min-height: 0;
}
.split-pane,
.aqua-split-pane {
  /* Vertical orientation (top/bottom) */
}
.split-pane.vertical,
.aqua-split-pane.vertical {
  flex-direction: column;
}
.split-pane.vertical .split-divider,
.split-pane.vertical .aqua-split-divider,
.aqua-split-pane.vertical .split-divider,
.aqua-split-pane.vertical .aqua-split-divider {
  width: 100%;
  height: 8px;
  min-width: auto;
  min-height: 8px;
  padding: 0;
  background: var(--split-divider-gradient-vertical);
  border-left: none;
  border-right: none;
  border-top: 1px solid var(--split-divider-border);
  border-bottom: 1px solid var(--split-divider-border);
  cursor: row-resize;
  flex-direction: column;
  gap: 1px;
}
.split-pane.vertical .split-grabber,
.split-pane.vertical .aqua-split-grabber,
.aqua-split-pane.vertical .split-grabber,
.aqua-split-pane.vertical .aqua-split-grabber {
  width: 20px;
  height: 1px;
  background: var(--split-grabber-bg);
  box-shadow: var(--split-grabber-shadow);
}

/* ============================================
   DIVIDER (horizontal by default - left/right panes)
   ============================================ */
.split-divider,
.aqua-split-divider {
  width: 8px;
  min-width: 8px;
  padding: 4px 0;
  background: var(--split-divider-gradient);
  border-left: 1px solid var(--split-divider-border);
  border-right: 1px solid var(--split-divider-border);
  cursor: col-resize;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  gap: 1px;
}

/* Grabber lines */
.split-grabber,
.aqua-split-grabber {
  width: 1px;
  height: 20px;
  background: var(--split-grabber-bg);
  box-shadow: var(--split-grabber-shadow-vertical);
}

/* ============================================
   LEGACY SPLIT VIEW (backward compatibility)
   ============================================ */
.split-view {
  display: flex;
  border: 1px solid var(--split-view-border);
  border-radius: var(--radius-medium);
  overflow: hidden;
  background: var(--split-view-bg);
}

.split-view-sidebar {
  width: 200px;
  min-width: 150px;
  background: var(--surface-light);
  border-right: 1px solid var(--split-view-sidebar-border);
  overflow: auto;
}

.split-view-content {
  flex: 1;
  overflow: auto;
  padding: 8px;
}

.split-view-divider {
  width: 8px;
  background: var(--split-divider-gradient);
  cursor: col-resize;
  border-left: 1px solid var(--split-divider-border);
  border-right: 1px solid var(--split-divider-border);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1px;
}
.split-view-divider:hover {
  background: var(--split-view-divider-hover-gradient);
}

/**
 * Disclosure
 * Expandable section with disclosure triangle
 */
details.disclosure {
  border: 1px solid var(--disclosure-border);
  border-radius: var(--radius-medium);
  background: var(--surface-light);
}
details.disclosure > summary {
  padding: 8px 12px;
  cursor: default;
  display: flex;
  align-items: center;
  gap: 8px;
  list-style: none;
  font-weight: bold;
}
details.disclosure > summary::-webkit-details-marker {
  display: none;
}
details.disclosure > summary::before {
  content: "";
  width: 8px;
  height: 8px;
  background-image: svg-load("./icon/disclosure-closed.svg");
  background-size: contain;
  background-repeat: no-repeat;
  transition: transform 0.2s;
}
details.disclosure > summary:hover {
  background: var(--surface);
}
details.disclosure[open] > summary::before {
  background-image: svg-load("./icon/disclosure-open.svg");
}
details.disclosure > .disclosure-content {
  padding: 12px;
  border-top: 1px solid var(--disclosure-divider);
}

/**
 * Titled Pane / Accordion
 * Mac OS X Aqua-style expandable panels
 * Matches JavaFX TitledPane and Accordion
 */
/* ============================================
   TITLED PANE CONTAINER
   ============================================ */
.titled-pane,
.aqua-titled-pane {
  border: 1px solid var(--titled-pane-border);
  border-width: 1px 0;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
}
.titled-pane + .titled-pane,
.aqua-titled-pane + .titled-pane {
  border-top: none;
}

/* ============================================
   TITLED PANE HEADER
   ============================================ */
.titled-pane-header,
.aqua-titled-pane-header {
  padding: 4px 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 6px;
  background: var(--titled-pane-header-gradient);
  user-select: none;
  position: relative;
  /* Aqua glass effect */
}
.titled-pane-header::before,
.aqua-titled-pane-header::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 50%;
  width: 100%;
  top: 0;
  background: linear-gradient(rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.05));
  border-radius: 0;
  z-index: 1;
  pointer-events: none;
}
.titled-pane-header::after,
.aqua-titled-pane-header::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 30%;
  width: 100%;
  bottom: 0;
  background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.2));
  border-radius: 0;
  z-index: 1;
  pointer-events: none;
}
.titled-pane-header:hover,
.aqua-titled-pane-header:hover {
  background: var(--titled-pane-header-hover-gradient);
}

/* ============================================
   DISCLOSURE ARROW
   ============================================ */
.titled-pane-arrow,
.aqua-titled-pane-arrow {
  width: 0;
  height: 0;
  border-left: 5px solid var(--titled-pane-arrow);
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  transition: transform 0.15s ease;
  flex-shrink: 0;
}

.titled-pane.expanded .titled-pane-arrow,
.aqua-titled-pane.expanded .aqua-titled-pane-arrow {
  transform: rotate(90deg);
}

/* ============================================
   TITLED PANE CONTENT
   ============================================ */
.titled-pane-content,
.aqua-titled-pane-content {
  padding: 12px;
  display: none;
  background: var(--titled-pane-content-bg);
  border-top: 1px solid var(--titled-pane-content-border);
}

.titled-pane.expanded .titled-pane-content,
.aqua-titled-pane.expanded .aqua-titled-pane-content {
  display: block;
}

/* ============================================
   ACCORDION (Multiple titled panes)
   ============================================ */
.accordion {
  border: 1px solid var(--accordion-border);
  border-radius: var(--radius-medium);
  overflow: hidden;
}
.accordion .titled-pane {
  border: none;
  border-bottom: 1px solid var(--accordion-divider);
}
.accordion .titled-pane:last-child {
  border-bottom: none;
}
.accordion .titled-pane-content {
  border-top: none;
}

/* ============================================
   DISABLED STATE
   ============================================ */
.titled-pane.disabled {
  opacity: 0.5;
  pointer-events: none;
}
.titled-pane.disabled .titled-pane-header {
  cursor: default;
}

.aqua-titled-pane.disabled {
  opacity: 0.5;
  pointer-events: none;
}
.aqua-titled-pane.disabled .aqua-titled-pane-header {
  cursor: default;
}

/**
 * Separator
 * Mac OS X Aqua-style horizontal and vertical dividers
 * Matches JavaFX Separator
 */
/* ============================================
   HORIZONTAL SEPARATOR (Default)
   ============================================ */
.separator,
.aqua-separator,
hr.aqua,
hr.aqua-separator {
  border: none;
  height: 1px;
  background: var(--separator-gradient-horizontal);
  margin: 8px 0;
}

/* ============================================
   VERTICAL SEPARATOR
   ============================================ */
.separator.vertical {
  width: 1px;
  height: 20px;
  margin: 0 8px;
  background: var(--separator-gradient-vertical);
  display: inline-block;
  vertical-align: middle;
}

.aqua-separator.vertical {
  width: 1px;
  height: 20px;
  margin: 0 8px;
  background: var(--separator-gradient-vertical);
  display: inline-block;
  vertical-align: middle;
}

/* ============================================
   MENU SEPARATOR
   ============================================ */
.menu-separator {
  height: 1px;
  background: var(--separator-menu-bg);
  margin: 4px 0;
}

/* ============================================
   TOOLBAR SEPARATOR
   ============================================ */
.toolbar-separator {
  width: 1px;
  height: 24px;
  margin: 0 4px;
  background: var(--separator-toolbar-gradient);
}

/* ============================================
   ETCHED SEPARATOR (3D effect)
   ============================================ */
.separator.etched {
  height: 2px;
  background: var(--separator-etched-gradient);
}

.separator.etched.vertical {
  width: 2px;
  height: 20px;
  background: var(--separator-etched-gradient-vertical);
}

.aqua-separator.etched {
  height: 2px;
  background: var(--separator-etched-gradient);
}

.aqua-separator.etched.vertical {
  width: 2px;
  height: 20px;
  background: var(--separator-etched-gradient-vertical);
}

/**
 * Hyperlink
 * Mac OS X Aqua-style hyperlinks
 * Matches JavaFX Hyperlink - underlines on hover/focus
 */
/* ============================================
   HYPERLINK BASE STYLES
   ============================================ */
a,
.hyperlink,
a.aqua-hyperlink {
  color: var(--hyperlink-color);
  text-decoration: none;
  cursor: pointer;
  font-family: var(--font-system);
}
a:hover, a:focus,
.hyperlink:hover,
.hyperlink:focus,
a.aqua-hyperlink:hover,
a.aqua-hyperlink:focus {
  text-decoration: underline;
}
a,
.hyperlink,
a.aqua-hyperlink {
  /* Focus ring */
}
a:focus,
.hyperlink:focus,
a.aqua-hyperlink:focus {
  outline: none;
  box-shadow: var(--hyperlink-focus-ring);
  border-radius: 2px;
}
a,
.hyperlink,
a.aqua-hyperlink {
  /* Visited state */
}
a:visited,
.hyperlink:visited,
a.aqua-hyperlink:visited {
  color: var(--hyperlink-visited);
}
a,
.hyperlink,
a.aqua-hyperlink {
  /* Active/pressed state */
}
a:active,
.hyperlink:active,
a.aqua-hyperlink:active {
  color: var(--hyperlink-active);
}

/* ============================================
   BUTTON STYLED AS LINK
   ============================================ */
button.link,
.button-link,
button.aqua-hyperlink {
  color: var(--hyperlink-color);
  text-decoration: none;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  font: inherit;
  font-family: var(--font-system);
  display: inline;
}
button.link:hover, button.link:focus,
.button-link:hover,
.button-link:focus,
button.aqua-hyperlink:hover,
button.aqua-hyperlink:focus {
  text-decoration: underline;
}
button.link:focus,
.button-link:focus,
button.aqua-hyperlink:focus {
  outline: none;
  box-shadow: var(--hyperlink-focus-ring);
  border-radius: 2px;
}
button.link:active,
.button-link:active,
button.aqua-hyperlink:active {
  color: var(--hyperlink-active);
}

/* ============================================
   DISABLED HYPERLINK
   ============================================ */
a.disabled,
.hyperlink.disabled,
button.link:disabled,
.button-link:disabled,
button.aqua-hyperlink:disabled,
button.aqua-hyperlink.disabled {
  color: var(--hyperlink-disabled);
  opacity: 0.6;
  cursor: not-allowed;
  pointer-events: none;
  text-decoration: none;
}
a.disabled:hover, a.disabled:focus,
.hyperlink.disabled:hover,
.hyperlink.disabled:focus,
button.link:disabled:hover,
button.link:disabled:focus,
.button-link:disabled:hover,
.button-link:disabled:focus,
button.aqua-hyperlink:disabled:hover,
button.aqua-hyperlink:disabled:focus,
button.aqua-hyperlink.disabled:hover,
button.aqua-hyperlink.disabled:focus {
  text-decoration: none;
}

/* ============================================
   HYPERLINK VARIATIONS
   ============================================ */
/* Subtle link - no underline even on hover */
.hyperlink.subtle:hover, .hyperlink.subtle:focus {
  text-decoration: none;
  opacity: 0.8;
}

/* External link indicator */
.hyperlink.external::after {
  content: " ";
  font-size: 0.8em;
}

/**
 * Window Pane
 * Navigation pane with nested menu items
 */
.window-pane {
  background: var(--surface-light);
  border: 1px solid var(--window-pane-border);
  border-radius: var(--radius-medium);
  padding: 8px 0;
}
.window-pane .menu-items {
  list-style: none;
  margin: 0;
  padding: 0;
}
.window-pane .menu-items li {
  padding: 4px 12px;
  cursor: default;
}
.window-pane .menu-items li a {
  color: var(--text-color);
  text-decoration: none;
  display: block;
}
.window-pane .menu-items li a:hover {
  color: var(--aqua-blue);
}
.window-pane .menu-items li:hover {
  background: var(--window-pane-hover-bg);
}
.window-pane .menu-items {
  /* Nested lists */
}
.window-pane .menu-items ul {
  list-style: none;
  margin: 4px 0 0 0;
  padding: 0 0 0 12px;
}
.window-pane .menu-items ul li {
  padding: 2px 12px;
  font-size: var(--font-size-small);
}
.window-pane .menu-items ul li::before {
  content: "";
  display: inline-block;
  width: 6px;
  height: 6px;
  background: var(--window-pane-bullet);
  border-radius: 50%;
  margin-right: 8px;
  vertical-align: middle;
}
.window-pane .menu-items ul li a {
  display: inline;
}
.window-pane .menu-items {
  /* Section headers (non-link items) */
}
.window-pane .menu-items > li:not(:has(> a)):not(:has(ul)) {
  font-weight: bold;
  color: var(--text-color);
  padding-top: 8px;
}
.window-pane .menu-items > li:not(:has(> a)):not(:has(ul)):first-child {
  padding-top: 4px;
}
.window-pane .menu-items {
  /* Category items with nested lists */
}
.window-pane .menu-items > li:has(> ul) {
  font-weight: bold;
  padding-top: 8px;
}
.window-pane .menu-items > li:has(> ul):hover {
  background: transparent;
}

/**
 * Toast Notifications
 * Mac OS X Aqua-style toast notifications with pinstripe background
 */
.aqua-toast-container {
  position: fixed;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 16px;
  pointer-events: none;
}

.aqua-toast-container--top-left {
  top: 0;
  left: 0;
  align-items: flex-start;
}

.aqua-toast-container--top-right {
  top: 0;
  right: 0;
  align-items: flex-end;
}

.aqua-toast-container--top-center {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  align-items: center;
}

.aqua-toast-container--bottom-left {
  bottom: 0;
  left: 0;
  align-items: flex-start;
  flex-direction: column-reverse;
}

.aqua-toast-container--bottom-right {
  bottom: 0;
  right: 0;
  align-items: flex-end;
  flex-direction: column-reverse;
}

.aqua-toast-container--bottom-center {
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  align-items: center;
  flex-direction: column-reverse;
}

/* Toast base */
.aqua-toast {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  min-width: 280px;
  max-width: 400px;
  padding: 12px 14px;
  border-radius: 8px;
  font-family: "Lucida Grande", "Lucida Sans Unicode", sans-serif;
  font-size: 13px;
  pointer-events: auto;
  /* Semi-transparent pinstripe background */
  background: var(--toast-bg);
  background-image: var(--toast-pinstripe);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid var(--toast-border);
  box-shadow: var(--toast-shadow);
  animation: aqua-toast-enter 0.2s ease-out;
}

.aqua-toast--leaving {
  animation: aqua-toast-leave 0.2s ease-in forwards;
}

@keyframes aqua-toast-enter {
  from {
    opacity: 0;
    transform: translateY(10px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
@keyframes aqua-toast-leave {
  from {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
  to {
    opacity: 0;
    transform: translateY(-10px) scale(0.95);
  }
}
/* Toast icon */
.aqua-toast-icon {
  flex-shrink: 0;
  width: 18px;
  height: 18px;
  margin-top: 1px;
}

.aqua-toast-icon svg {
  width: 100%;
  height: 100%;
}

.aqua-toast-icon--success {
  color: var(--toast-success);
}

.aqua-toast-icon--error {
  color: var(--toast-error);
}

.aqua-toast-icon--warning {
  color: var(--toast-warning);
}

.aqua-toast-icon--info {
  color: var(--toast-info);
}

/* Toast content */
.aqua-toast-content {
  flex: 1;
  min-width: 0;
}

.aqua-toast-title {
  font-weight: 600;
  color: var(--toast-title-color);
  line-height: 1.3;
}

.aqua-toast-description {
  margin-top: 2px;
  color: var(--toast-description-color);
  line-height: 1.4;
}

/* Toast action button - Aqua style */
.aqua-toast-action {
  flex-shrink: 0;
  height: 24px;
  padding: 0 12px;
  border: none;
  border-radius: 12px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.15s ease;
  position: relative;
  overflow: hidden;
  /* Aqua primary button gradient */
  background: var(--toast-action-gradient);
  color: var(--toast-action-text);
  text-shadow: var(--toast-action-text-shadow);
  box-shadow: var(--toast-action-shadow);
  /* Aqua glass effect (top shine only) */
}
.aqua-toast-action::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  top: 5%;
  background: linear-gradient(rgb(255, 255, 255), rgba(255, 255, 255, 0.375));
  border-radius: inherit;
  filter: blur(1px);
  z-index: 1;
  pointer-events: none;
}
.aqua-toast-action:hover {
  box-shadow: var(--toast-action-shadow-hover);
}
.aqua-toast-action:active {
  filter: brightness(0.9);
}

/* Toast close button */
.aqua-toast-close {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  padding: 0;
  border: none;
  background: transparent;
  color: var(--toast-close-color);
  cursor: pointer;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.15s ease;
}
.aqua-toast-close:hover {
  background: var(--toast-close-hover-bg);
  color: var(--toast-close-hover-color);
}
.aqua-toast-close svg {
  width: 12px;
  height: 12px;
}

/* Type-specific styling */
.aqua-toast--success {
  border-left: 3px solid var(--toast-success);
}

.aqua-toast--error {
  border-left: 3px solid var(--toast-error);
}

.aqua-toast--warning {
  border-left: 3px solid var(--toast-warning);
}

.aqua-toast--info {
  border-left: 3px solid var(--toast-info);
}

/* Compact variant */
.aqua-toast--compact {
  padding: 8px 12px;
  min-width: 200px;
}
.aqua-toast--compact .aqua-toast-icon {
  width: 16px;
  height: 16px;
}
.aqua-toast--compact .aqua-toast-title {
  font-size: 12px;
}
.aqua-toast--compact .aqua-toast-description {
  font-size: 11px;
}

/**
 * Chat Bubble
 * Mac OS X Aqua-style chat bubbles with glossy effects
 */
.aqua-chat-bubble {
  position: relative;
  border-radius: 16px;
  overflow: hidden;
  padding: 6px 12px;
  min-height: 29.875px;
  font-family: "Lucida Grande", "Helvetica Neue", sans-serif;
  font-size: 13px;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  text-shadow: var(--chat-bubble-text-shadow);
  /* Enhanced shadow with subtle outer lift and multi-layer inset stroke */
  box-shadow: var(--chat-bubble-shadow);
  /* Aqua glass effect (top shine + bottom glow) */
}
.aqua-chat-bubble::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  top: 5%;
  background: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.3));
  border-radius: inherit;
  filter: blur(1px);
  z-index: 1;
  pointer-events: none;
}
.aqua-chat-bubble::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5));
  border-radius: inherit;
  filter: blur(3px);
  z-index: 1;
  pointer-events: none;
}
.aqua-chat-bubble {
  /* Ensure inner text stays above shine */
}
.aqua-chat-bubble > * {
  position: relative;
  z-index: 2;
}

/* Color variants */
.aqua-chat-bubble--yellow {
  background-color: var(--chat-bubble-yellow);
}

.aqua-chat-bubble--blue {
  background-color: var(--chat-bubble-blue);
}

.aqua-chat-bubble--pink {
  background-color: var(--chat-bubble-pink);
}

.aqua-chat-bubble--purple {
  background-color: var(--chat-bubble-purple);
}

.aqua-chat-bubble--indigo {
  background-color: var(--chat-bubble-indigo);
}

.aqua-chat-bubble--teal {
  background-color: var(--chat-bubble-teal);
}

.aqua-chat-bubble--lime {
  background-color: var(--chat-bubble-lime);
}

.aqua-chat-bubble--amber {
  background-color: var(--chat-bubble-amber);
}

.aqua-chat-bubble--cyan {
  background-color: var(--chat-bubble-cyan);
}

.aqua-chat-bubble--rose {
  background-color: var(--chat-bubble-rose);
}

.aqua-chat-bubble--gray {
  background-color: var(--chat-bubble-gray);
}

.aqua-chat-bubble--green {
  background-color: var(--chat-bubble-green);
}

.aqua-chat-bubble--orange {
  background-color: var(--chat-bubble-orange);
}

.aqua-chat-bubble--red {
  background-color: var(--chat-bubble-red);
}

/* Alignment - tail direction */
.aqua-chat-bubble--left {
  border-bottom-left-radius: 4px;
}

.aqua-chat-bubble--right {
  border-bottom-right-radius: 4px;
}

/* Size variations */
.aqua-chat-bubble--small {
  padding: 4px 10px;
  font-size: 12px;
  min-height: 24px;
  border-radius: 12px;
}
.aqua-chat-bubble--small::before {
  height: 10px;
  left: 5px;
  right: 5px;
  border-radius: 12px 12px 3px 3px;
}

.aqua-chat-bubble--large {
  padding: 10px 16px;
  font-size: 14px;
  min-height: 40px;
  border-radius: 20px;
}
.aqua-chat-bubble--large::before {
  height: 16px;
  left: 8px;
  right: 8px;
  border-radius: 20px 20px 5px 5px;
}

/* Message metadata */
.aqua-chat-bubble-meta {
  font-size: 11px;
  color: var(--chat-bubble-meta-text);
  margin-bottom: 4px;
  display: flex;
  gap: 8px;
  align-items: center;
}

.aqua-chat-bubble-username {
  font-weight: 600;
}

.aqua-chat-bubble-time {
  opacity: 0.7;
}

/* Chat wrapper for proper alignment */
.aqua-chat-message {
  display: flex;
  flex-direction: column;
  max-width: 70%;
}

.aqua-chat-message--right {
  align-items: flex-end;
  margin-left: auto;
}

.aqua-chat-message--left {
  align-items: flex-start;
  margin-right: auto;
}

/**
 * Pagination
 * Mac OS X Aqua-style pagination controls - JavaFX AquaFX accurate
 */
/* ============================================
   PAGINATION CONTAINER
   ============================================ */
.pagination,
.aqua-pagination {
  display: flex;
  align-items: center;
  gap: 3px;
  font-family: var(--font-system);
  font-size: var(--font-size-small);
}

/* ============================================
   PAGINATION BUTTONS (Number buttons)
   ============================================ */
.pagination-btn,
.aqua-pagination-btn {
  border: 0.5px solid var(--pagination-border);
  border-radius: 3em;
  background: var(--pagination-bg);
  background: var(--pagination-gradient);
  padding: 2px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  font-size: 11px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  outline: none;
  transition: all 0.15s ease;
  position: relative;
  overflow: hidden;
  /* Aqua glass effect */
}
.pagination-btn::before,
.aqua-pagination-btn::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 40%;
  width: 75%;
  top: 8%;
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
  border-radius: 50% 50% 30% 30%;
  filter: blur(0.5px);
  z-index: 2;
  pointer-events: none;
}
.pagination-btn::after,
.aqua-pagination-btn::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 35%;
  width: 70%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.35));
  border-radius: 30% 30% 50% 50%;
  filter: blur(1px);
  pointer-events: none;
}
.pagination-btn:hover:not(.active):not(:disabled),
.aqua-pagination-btn:hover:not(.active):not(:disabled) {
  background: var(--pagination-hover-gradient);
}
.pagination-btn,
.aqua-pagination-btn {
  /* Active/selected state - Aqua blue */
}
.pagination-btn.active,
.aqua-pagination-btn.active {
  border-color: var(--pagination-active-border);
  background: var(--pagination-active-gradient);
}
.pagination-btn,
.aqua-pagination-btn {
  /* Disabled state */
}
.pagination-btn:disabled,
.aqua-pagination-btn:disabled {
  opacity: 0.6;
  cursor: default;
  pointer-events: none;
}
.pagination-btn,
.aqua-pagination-btn {
  /* Focus state */
}
.pagination-btn:focus,
.aqua-pagination-btn:focus {
  box-shadow: var(--pagination-focus-ring);
}

/* ============================================
   PAGINATION ARROW BUTTONS
   ============================================ */
.pagination-arrow,
.aqua-pagination-arrow {
  border: 0.5px solid var(--pagination-border);
  border-radius: 3em;
  background: var(--pagination-arrow-gradient);
  padding: 2px 5px;
  cursor: pointer;
  font-size: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  outline: none;
  min-width: 20px;
  height: 20px;
  transition: all 0.15s ease;
  position: relative;
  overflow: hidden;
  /* Aqua glass effect */
}
.pagination-arrow::before,
.aqua-pagination-arrow::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 40%;
  width: 75%;
  top: 8%;
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
  border-radius: 50% 50% 30% 30%;
  filter: blur(0.5px);
  z-index: 2;
  pointer-events: none;
}
.pagination-arrow::after,
.aqua-pagination-arrow::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 35%;
  width: 70%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.35));
  border-radius: 30% 30% 50% 50%;
  filter: blur(1px);
  pointer-events: none;
}
.pagination-arrow:hover:not(:disabled),
.aqua-pagination-arrow:hover:not(:disabled) {
  background: var(--pagination-arrow-hover-gradient);
}
.pagination-arrow:active:not(:disabled),
.aqua-pagination-arrow:active:not(:disabled) {
  background: var(--pagination-arrow-active-gradient);
  border-color: var(--pagination-active-border);
}
.pagination-arrow:disabled,
.aqua-pagination-arrow:disabled {
  opacity: 0.6;
  cursor: default;
  pointer-events: none;
}
.pagination-arrow:focus,
.aqua-pagination-arrow:focus {
  box-shadow: var(--pagination-focus-ring);
}

/* ============================================
   PAGINATION INFO TEXT
   ============================================ */
.pagination-info {
  font-family: var(--font-system);
  font-size: 11px;
  color: var(--text-color);
  margin: 0 8px;
}

/* ============================================
   PAGINATION ELLIPSIS
   ============================================ */
.pagination-ellipsis {
  width: 20px;
  height: 20px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  color: var(--pagination-arrow-color);
}

/**
 * Badge
 * Mac OS X Aqua-style notification badge - JavaFX AquaFX accurate
 */
/* ============================================
   BADGE BASE STYLES
   ============================================ */
.badge,
.aqua-badge {
  display: inline-block;
  background: var(--badge-gradient);
  color: var(--badge-text);
  font-family: var(--font-system);
  font-size: 10px;
  font-weight: bold;
  padding: 2px 6px;
  border-radius: 10px;
  min-width: 16px;
  text-align: center;
  line-height: 1.2;
  box-shadow: var(--badge-shadow);
}

/* ============================================
   BADGE COLOR VARIANTS
   ============================================ */
/* Blue badge (info) */
.badge-blue,
.badge.blue,
.aqua-badge.blue {
  background: var(--badge-blue-gradient);
}

/* Green badge (success) */
.badge-green,
.badge.green,
.aqua-badge.green {
  background: var(--badge-green-gradient);
}

/* Orange badge (warning) */
.badge-orange,
.badge.orange,
.aqua-badge.orange {
  background: var(--badge-orange-gradient);
}

/* Gray badge (neutral) */
.badge-gray,
.badge.gray,
.aqua-badge.gray {
  background: var(--badge-gray-gradient);
}

/* ============================================
   BADGE SIZE VARIANTS
   ============================================ */
/* Small badge */
.badge-small,
.badge.small,
.aqua-badge.small {
  font-size: 9px;
  padding: 1px 4px;
  min-width: 12px;
  border-radius: 8px;
}

/* Large badge */
.badge-large,
.badge.large,
.aqua-badge.large {
  font-size: 12px;
  padding: 3px 8px;
  min-width: 20px;
  border-radius: 12px;
}

/* ============================================
   BADGE POSITIONING (for use with icons/buttons)
   ============================================ */
.badge-wrapper {
  position: relative;
  display: inline-block;
}
.badge-wrapper .badge,
.badge-wrapper .aqua-badge {
  position: absolute;
  top: -6px;
  right: -6px;
  z-index: 1;
}
.badge-wrapper {
  /* Small variant positioning */
}
.badge-wrapper .badge.small,
.badge-wrapper .badge-small,
.badge-wrapper .aqua-badge.small {
  top: -4px;
  right: -4px;
}

/* ============================================
   EMPTY BADGE (dot indicator)
   ============================================ */
.badge-dot,
.badge.dot,
.aqua-badge.dot {
  width: 8px;
  height: 8px;
  min-width: 8px;
  padding: 0;
  border-radius: 50%;
}

/* Utilities & Themes */
/**
 * Utility Classes
 * Layout helpers and common patterns
 */
.field-row {
  display: flex;
  align-items: center;
  gap: var(--grouped-element-spacing);
}
.field-row + .field-row {
  margin-top: var(--element-spacing);
}

.field-row-stacked {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.field-row-stacked + .field-row-stacked {
  margin-top: var(--element-spacing);
}

.aqua-color-swatch {
  width: 30px;
  height: 20px;
  border: 1px solid var(--color-swatch-border);
  border-radius: 3px;
  box-shadow: var(--color-swatch-shadow);
  display: inline-block;
}

/* Era-specific visibility utilities */
/* Show only in eras before 10.6 (10-0, 10-2, 10-3, 10-4) */
.era-pre-10-6 {
  display: block;
}

/* Show only in 10.6 and later */
.era-10-6-plus {
  display: none;
}

/* 10.6 era: swap visibility */
[data-aqua-era="10-6"] .era-pre-10-6 {
  display: none;
}

[data-aqua-era="10-6"] .era-10-6-plus {
  display: block;
}

